import React, {Fragment} from "react";
import {AlertTriangle} from "react-feather";


const IpkoFail = ({status}) => {


    return (
        <Fragment>
            {
                status === 'broke_user' ?
                    <div className={'ipko_fail'}>
                        <AlertTriangle size={'120px'} color={'#db912c'}/>
                        <div className="ipko_fail_content">
                            <h3>Błąd, bank jest przeciążony</h3>
                            <p>Bank nie może przetworzyć Twojej prośby. <br/> Spróbuj ponownie później lub wybierz inny
                                bank.</p>
                        </div>
                    </div> : null

            }

        </Fragment>
    )
}
export default IpkoFail;