import React, {Fragment} from "react";
import '../../assets/scss/pages/_bankID.scss'
import PageTitle from "../../components/page-title";
import {Col, Container, Row} from "reactstrap";
import CardBank from "../../components/card-bank";
// logos
import pekao_logo from '../../assets/images/banks/Pekao_Logo.svg'
import ing_logo from '../../assets/images/banks/ing_logo.svg'
import ipko_logo from '../../assets/images/banks/ipko_logo.svg'
import bnp_logo from '../../assets/images/banks/bnp_logo.svg'
import mbank_logo from '../../assets/images/banks/mbank_logo.svg'
import santander_logo from '../../assets/images/banks/santander-logo.svg'
import alior_logo from '../../assets/images/banks/alior-logo.svg'
import agricole_logo from '../../assets/images/banks/credit-agricole-logo.svg'
import inteligo_logo from '../../assets/images/banks/inteligo-bank-logo-azul.png'
import getin_logo from '../../assets/images/banks/getin_logo.svg'
import millennium_logo from '../../assets/images/banks/millenium_logo.svg'
import {useModal} from "../../hooks/useModal";


const BankID = () => {
    const {openBank, isOpen, Modal} = useModal();

    console.log(isOpen);
    return (
        <Fragment>
            <PageTitle title={'BankID'}/>
            <section className={'prevention-section sp-two'}>
                <Container>
                    <Row>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Pekao24'} logo={pekao_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'ING Bank'} logo={ing_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'PKO Bank'} logo={ipko_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'BNP PARIBAS'} logo={bnp_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'MBank'} logo={mbank_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Santander'} logo={santander_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Alior'} logo={alior_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Credit Agricole'} logo={agricole_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Inteligo'} logo={inteligo_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Get In Bank'} logo={getin_logo} open={openBank}/>
                        </Col>
                        <Col sm={12} md={6} lg={4}>
                            <CardBank title={'Millennium Bank'} logo={millennium_logo} open={openBank}/>
                        </Col>
                    </Row>
                </Container>
            </section>
            <Modal/>
        </Fragment>
    )
}

export default BankID;