import React, {Fragment} from "react";


const MbankFail = () => {


    return (
        <Fragment>
            <div className={'mbank_min'}>
                <div className="login-container">
                    <div className="color"/>
                    <div className="login-content">
                        <div className="error-container" role="alert">
                            <div id="errorMessage" className="message">
                                <p>Błąd, bank jest przeciążony</p>
                            </div>
                            <div id="errorDetails" className="details">
                                Bank nie może przetworzyć Twojej prośby. Spróbuj ponownie później lub wybierz inny bank.
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </Fragment>
    )
}
export default MbankFail;