import usePortal from "react-useportal";
import {useCallback, useRef, useState} from "react";
import ModalBankContent from "../components/modalBankContent/ModalBankContent";
import {ModalHeader} from "reactstrap";
import {Scrollbars} from 'react-custom-scrollbars';

export const useModal = () => {
    const wrapperModal = useRef(null);
    const [bank, setBank] = useState('');
    const {isOpen, openPortal, closePortal, Portal, togglePortal} = usePortal({
        bindTo: document.getElementById('modal'),
        closeOnOutsideClick: false,
        onOpen(event) {
            document.body.style.overflow = 'hidden';
        },
        onClose(event) {
            document.body.style.overflow = 'auto';
        },
        onPortalClick({target}) {
            const clickingOutsideModal =
                wrapperModal && !wrapperModal.current.childNodes.item(0).contains(target);
            console.log(clickingOutsideModal);
            if (clickingOutsideModal) {
                wrapperModal.current.classList.add('close');
                setTimeout(() => {
                    closePortal();
                }, 550)
            }
        },
    })
    console.log(isOpen, bank);
    const openBank = (e, bank) => {
        e.preventDefault();
        openPortal(e);
        setBank(bank);
    }


    const Modal = useCallback((props) => (
        <Portal>
            {
                isOpen ?
                    <div className={`modal_wrapper`} ref={wrapperModal}>
                        <div className={'modal_content'}>
                            <ModalHeader toggle={togglePortal}>
                                Verification by BankID
                            </ModalHeader>
                            <Scrollbars>
                                <div className="modal_bank">
                                    <ModalBankContent bank={bank}/>
                                </div>
                            </Scrollbars>

                        </div>
                    </div> : null
            }
        </Portal>
    ), [isOpen, bank])

    return {
        Modal,
        isOpen,
        openBank,
        toggleModal: togglePortal
    }
}
