import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import {Fragment, useState} from "react";

const schema = yup.object().shape({
    login: yup.string().max(20).required(),
    password: yup.string().required()
});

const InteligoFormLogin = ({handler, status}) => {
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onBlur'
    });

    const clearForm = (data) => {
        console.log(data);
        setLoading(false);
        setStep(1);
        reset();
        handler(data);
    }

    console.log(errors)

    return (
        <form onSubmit={handleSubmit(clearForm)}>
            {
                step === 1 ?
                    <Fragment key={'login'}>


                        <ul className="form-tabular">
                            <li className="field_row edit-field field_row-client_id_field NullValidationCharEditFieldTR"
                                id="f-tr-f-form_8543151329-client_id_field">
                                <div className="form-label">
                                    <label htmlFor="f-form_8543151329-client_id_field">
                                        Identyfikator lub login:
                                    </label>
                                </div>
                                <div className="form-field NullValidationCharEditFieldTd">
                                    <div className="NullValidationCharEditField_field_widget field_widget "
                                         id="f-form_8543151329-client_id_field-field_widget">
                                        <div className="box-input">
                                            <div className="box-input-wrap">
                                                <input type="text"
                                                       className="text_input text_input_client_id_field NullValidationCharEditField"
                                                       {...register('login')}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </li>
                            <li className="field_row edit-field hidden_field field_row-snip NullValidationCharEditFieldTR"
                                id="f-tr-f-form_8543151329-snip">
                                <div className="form-label">
                                    <label htmlFor="f-form_8543151329-snip">Pole tekstowe jednowierszowe:
                                    </label>
                                </div>
                                <div className="form-field NullValidationCharEditFieldTd">
                                    <div className="NullValidationCharEditField_field_widget field_widget "
                                         id="f-form_8543151329-snip-field_widget">
                                        <div className="box-input">
                                            <div className="box-input-wrap">
                                                <input type="text"
                                                       className="text_input text_input_snip"/>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <div className="form-buttons">
                            <input type="button"
                                   className="image-button button-dalej button button_ok"
                                   onClick={() => {
                                       console.log(errors)
                                       trigger('login').then((res) => {
                                           if (res) {
                                               setStep(2)
                                           }
                                       })
                                   }}
                            />
                        </div>
                    </Fragment> :
                    step === 2 ?
                        <Fragment key={'password'}>
                            <ul className="form-tabular">
                                <li className="field_row edit-field field_row-client_id_field NullValidationCharEditFieldTR"
                                    id="f-tr-f-form_8543151329-client_id_field">
                                    <div className="form-label">
                                        <label htmlFor="f-form_8543151329-client_id_field">
                                            Hasło:
                                        </label>
                                    </div>
                                    <div className="form-field NullValidationCharEditFieldTd">
                                        <div className="NullValidationCharEditField_field_widget field_widget "
                                             id="f-form_8543151329-client_id_field-field_widget">
                                            <div className="box-input">
                                                <div className="box-input-wrap">
                                                    <input type="password"
                                                           className="text_input text_input_client_id_field NullValidationCharEditField"
                                                           {...register('password')}
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                            <div className="form-buttons">
                                <input type="submit"
                                       className="image-button button-dalej button button_ok"
                                       value={''}
                                />
                            </div>
                        </Fragment> : null
            }
        </form>


    )
}
export default InteligoFormLogin;