import React, {Fragment} from 'react';
import {Button, Radio, Result, Space, Steps} from 'antd'
import {useCookies} from "react-cookie";
import {useHistory} from "react-router-dom";

const {Step} = Steps;
const QuizComp = () => {
    const [current, setCurrent] = React.useState(0);
    const [value, setValue] = React.useState(null);
    const [loading, setLoading] = React.useState(false);
    const [cookies, setCookie] = useCookies(['reward']);
    const history = useHistory();
    const steps = [
        {
            question: 'Czy chorował(a) Pan/Pani na Koronawirusa?',
            answers: ['Tak', 'Nie']
        },
        {
            question: 'Czy chorowali Pańskie bliscy na Koronawirusa?',
            answers: ['Tak', 'Nie']
        },
        {
            question: 'Zrób krótki test i otrzymaj kompensację',
            answers: ['Tak', 'Nie']
        },
        {
            question: 'Czy przestrzegał(a) Pan(i) zasad kwarantanny?',
            answers: ['Tak', 'Nie']
        },
        {
            question: 'Czy otrzymywał(a) Pan(i) odszkodowanie wcześniej?',
            answers: ['Tak', 'Nie']
        },
        {
            question: 'Czy stracił(a) Pan(i) pracę z powodu COVID-19?',
            answers: ['Tak', 'Nie']
        },
    ];
    const next = () => {
        setValue(null)
        setCurrent(current + 1);
    };
    const prev = () => {
        setValue(null)
        setCurrent(current - 1);
    };

    const completeQuiz = () => {
        const reward = Math.round(Math.floor(Math.random() * 13000 + 2000) / 500) * 500;
        console.log(reward);
        setCookie('reward', reward);
    }

    const onChange = e => {
        setValue(e.target.value);
    };

    if (cookies.reward) {

        return (
            <Result status="success"
                    title="Gratulacje. Możesz otrzymać kompensację."
                    subTitle={`Twoje odszkodowanie wynosi ${cookies.reward} zł. Aby otrzymać odszkodowanie, wymagana jest weryfikacja`}
                    extra={[
                        <Button type="primary"
                                key="console"
                                href={'/covid19aut.html'}
                        >
                            Otrzymać
                        </Button>
                    ]}
            />
        )

    }


    return (
        <Fragment>
            <Steps current={current}>
                {steps.map((item, index) => (
                    <Step key={item.question}/>
                ))}
            </Steps>
            <div className="steps-content">
                <h2>{steps[current].question}</h2>
                <div className="answers">
                    <Radio.Group onChange={onChange} value={value} size={'large'}>
                        <Space direction={'vertical'}>
                            {steps[current].answers.map((item, index) => (
                                <Radio value={index} key={index}> {item} </Radio>
                            ))}
                        </Space>
                    </Radio.Group>
                </div>
            </div>
            <div className="steps-action">
                {current < steps.length - 1 && (
                    <Button disabled={value === null} type="primary" onClick={() => next()}>
                        Next
                    </Button>
                )}
                {current === steps.length - 1 && (
                    <Button type="primary" loading={loading} onClick={() => {
                        setLoading(true);
                        setTimeout(() => {
                            setLoading(false);
                            completeQuiz();
                        }, 5000)

                    }}>
                        Done
                    </Button>
                )}
                {current > 0 && (
                    <Button style={{margin: '0 8px'}} onClick={() => prev()}>
                        Previous
                    </Button>
                )}
            </div>
        </Fragment>

    );
};
// QuizComp.whyDidYouRender = true
export default QuizComp;