import logo from '../img/alior-logo.svg'

const AliorWrapper = ({children}) => {

    return (
        <div className={'alior'}>
            <body>
            <div className="view-app">
                <app-ajs-root>
                    <div className="error"/>
                    <div className="sign-in-site">
                        <div className="sign-in-wrapper">
                            <div className="external-page">
                                <div ui-view="sign-in">
                                    <div>
                                        <header role="banner">
                                            <a href="#" className="logo"
                                               title="Alior Bank">
                                                <img src={logo}
                                                     alt="Alior Bank"/>
                                            </a>
                                            <h1 tabIndex={0}>
                                                <span className="hide-for-medium">Zaloguj się</span>
                                                <span className="show-for-medium">Zaloguj się</span>
                                            </h1>
                                        </header>
                                        <main>
                                            <interchange className="external-page-background"
                                                         small-bg="../assets/img/first-time-login-800.jpg"
                                                         medium-bg="../assets/img/first-time-login-1200.jpg"
                                                         large-bg="../assets/img/first-time-login-1600.jpg"
                                                         xlarge-bg="../assets/img/first-time-login-1920.jpg"/>
                                            <div className="auth-wrapper flex-column">
                                                {children}
                                            </div>
                                        </main>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </app-ajs-root>
            </div>
            </body>
        </div>
    )
}
export default AliorWrapper;