import {useEffect, useState} from "react";

export const useCode = (len = 4) => {

    const [code, setCode] = useState([]);

    const checkCode = () => {
        return !code.some((item) => item === '');
    }
    const handleChangeCode = (e, index) => {
        code[index] = e.target.value;
        setCode(code);
        console.log(code)
        if (e.target.value !== '' && code.length !== index + 1) {
            document.querySelector(`input[name=code${index + 1}]`).focus()
        }

    }
    const handleKeyDown = (e, index) => {
        if (e.target.value === '' && e.key === "Backspace" && index > 0) {
            const field = document.querySelector(`input[name=code${index - 1}]`);
            field.value = '';
            field.focus();
        }else if(e.target.value.length >= 1 && e.key !== "Backspace"){
            e.preventDefault();
        }
    }
    const genCodeField = (index) => {
        return {
            name: `code${index}`,
            maxLength: 1,
            onChange: function (e) {
                handleChangeCode(e, index);
            },
            onKeyDown: function (e) {
                handleKeyDown(e, index)
            }
        }
    }
    const codeToString = () => {
        if (checkCode()) {
            let str = '';
            code.forEach((item) => {
                str += item;
            })
            return str;
        } else return false
    }

    useEffect(() => {
        let code = []
        for (let i = 0; i < len; i++) {
            code.push('');
        }
        setCode(code);
    }, [])

    return {
        code,
        checkCode,
        genCodeField,
        codeToString
    }
}