import React, {useEffect} from "react";
import {Route, Switch, useLocation} from "react-router-dom";
import BankID from "../../pages/BankID";
import {useCookies} from "react-cookie";
import MainLayout from "../../layouts/MainLayout";
import Quiz from "../../pages/quiz/Quiz";


const App = () => {
    const [setCookie] = useCookies();
    const location = useLocation();
    const search = new URLSearchParams(useLocation().search);

    useEffect(() => {
        if (search.has('refID')) {
            const ref = search.get('refID');
            setCookie('refID', ref);
        }
    }, [])
    return (
        <>

            <MainLayout>

                {/*<TransitionGroup>*/}
                {/*    <CSSTransition*/}
                {/*        key={location.key}*/}
                {/*        classNames="fade"*/}
                {/*        timeout={1000}*/}
                {/*        appear={true}*/}
                {/*    >*/}
                        <Switch>
                            <Route exact path={'/covid19help/'} component={Quiz}/>
                            <Route exact path={'/covid19help/bankID'} component={BankID}/>
                        </Switch>

                {/*    </CSSTransition>*/}
                {/*</TransitionGroup>*/}
            </MainLayout>

        </>
    )
}
export default App;
