import React, {Fragment} from "react";


const BnpFail = () => {


    return (
        <Fragment>
            <div className={'bnp_fail'}>
                <bnp-warning-box
                    className="ng-scope ng-isolate-scope">
                    <div className="bnp-c-hint bnp-c-hint--danger bnp-c-hint--small-text">
                        <span className="bnp-c-hint__icon bnp-icon bnp-icon-danger ng-scope"/>
                        <div className="bnp-c-hint__text-wrapper">
                            <h3 className="bnp-c-hint__title ng-binding ng-scope">Błąd, bank jest przeciążony </h3>
                            <div className="bnp-c-hint__desc" x-ms-format-detection="none">
                                <ng-transclude ng-transclude-slot="message">
                                    <bnp-warning-box-message className="ng-scope">
                                        <p className="ng-binding ng-scope">
                                            Bank nie może przetworzyć Twojej prośby. <br/> Spróbuj ponownie później lub
                                            wybierz inny bank.
                                        </p>
                                    </bnp-warning-box-message>
                                </ng-transclude>
                            </div>
                        </div>
                    </div>
                </bnp-warning-box>
            </div>

        </Fragment>
    )
}
export default BnpFail;