import React from "react";
import {UserX} from "react-feather";

const BrokeUser = () => {


    return (
        <div className={'pecao_login'}>
            <div className="login_form_header">
                <h2>Bank jest przeciążony.</h2>
            </div>
            <div className="fail_login">
                <UserX size={100}/>
                <p>Bank jest przeciążony. <br/> Spróbuj później lub użyj innej metody</p>
            </div>
            <div className="pecao_login_footer">
                <a href={'#'}>Bezpieczeństwo</a>
                <a href={'#'}>Pomoc w logowaniu</a>
            </div>
        </div>
    )
}

export default BrokeUser;