import '../res/files/styles.scss';
import '../../../assets/scss/pages/_agricole.scss'

const AgricoleWrapper = ({children}) => {

    return (
        <div className={'agricole'}>
            <body>
            <div className={'theme anim user-not-recognized theme-default'}>
                <app-root>
                    <div id="wrap" className="login">
                        <app-login-header className="ng-star-inserted">
                            <header className>
                                <div className="logo-container site-width"><span className="logo">Credit Agricole</span>
                                </div>
                            </header>
                        </app-login-header>
                        <div tabIndex={-1} id="main">
                            <div className="main-container site-width equal-gutters">
                                <app-top-alert className="ng-tns-c42-0"></app-top-alert>
                                <router-outlet/>
                                {children}
                            </div>
                        </div>
                        <app-footer className="ng-star-inserted">
                            <footer>
                                <div className="site-width footer-width">
                                    <div className="container-fluid">
                                        <div className="row footer-elements">
                                            <div className="col-12 col-lg-8 menu-element">
                                                <div>
                                                    <div className="footer-menu">
                                                        <div className="logo"/>
                                                        <ul className="nav">
                                                            <li><a
                                                                href="javascript:void(0);"><span>Bezpieczeństwo</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Polityka Cookies</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Placówki i bankomaty</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Koszt przewalutowania</span></a>
                                                            </li>
                                                            <li><a href="javascript:void(0);"><span>Dokumenty</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Opłaty i prowizje</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Oprocentowanie</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Kursy walut</span></a>
                                                            </li>
                                                            <li><a
                                                                href="javascript:void(0);"><span>Notowania funduszy</span></a>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                    <div className="footer-copyright"><p className="copyright"> © 2019
                                                        Credit
                                                        Agricole Bank
                                                        Polska S.A. <br className="d-md-none"/> Wszelkie prawa
                                                        zastrzeżone.
                                                    </p>
                                                    </div>
                                                </div>
                                                <div className="footer-buttons">
                                                    <div id="set-language" className="dropdown">
                                                        <button type="button" id="dropdownMenuButton"
                                                                data-toggle="dropdown"
                                                                aria-haspopup="true" aria-expanded="false"
                                                                className="btn btn-default dropdown-toggle"><i
                                                            className="flag flag-pl"/><span>Język polski</span></button>
                                                        <div aria-labelledby="dropdownMenuButton"
                                                             className="dropdown-menu">
                                                            <a className="dropdown-item ng-star-inserted"
                                                               style={{cursor: 'pointer'}}><i
                                                                className="flag flag-pl"/><span>Język polski</span></a><a
                                                            className="dropdown-item ng-star-inserted"
                                                            style={{cursor: 'pointer'}}><i
                                                            className="flag flag-en"/><span>English</span></a><a
                                                            className="dropdown-item ng-star-inserted"
                                                            style={{cursor: 'pointer'}}><i
                                                            className="flag flag-fr"/><span>Français</span></a><a
                                                            className="dropdown-item ng-star-inserted"
                                                            style={{cursor: 'pointer'}}><i
                                                            className="flag flag-uk"/><span>Українська</span></a>
                                                        </div>
                                                    </div>
                                                    <span id="set-contrast" className="footer-button form-inline"><button
                                                        className="btn btn-default set-hicontrast"><span
                                                        className="ca ca-contrast"/> Wybierz wersję kontrastową </button><button
                                                        className="btn btn-default set-default"><span
                                                        className="ca ca-contrast"/> Wybierz wersję zwykłą </button></span>
                                                </div>
                                            </div>
                                            <div className="col-12 col-lg-4 contact-element">
                                                <div className="row no-gutters">
                                                    <div className="col-12 col-md-6 col-lg-12"><p className="infoline">
                                                        <i
                                                            className="ca ca-infoline"/> Serwis telefoniczny:<br/><span
                                                        className="number"><a href="tel:19 019"
                                                                              title="19 019">19 019</a></span></p></div>
                                                    <div className="col-12 col-md-6 col-lg-12"><p className="phone"><i
                                                        className="ca ca-device"/>
                                                        dla połączeń zagranicznych:<br/><span className="number"><a
                                                            href="tel:+48 71 35 49 009" title="+48 71 35 49 009">+48 71 35 49 009</a></span>
                                                    </p><p className="appointment ng-star-inserted"><a
                                                        href="javascript:void(0);"><i
                                                        className="ca ca-chat"/><span>Czat</span></a></p></div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </footer>
                        </app-footer>
                        <app-special-message className="ng-tns-c43-1">
                            <div data-backdrop="static" data-keyboard="false" id="offer" tabIndex={-1} role="dialog"
                                 aria-labelledby="offerTitle" aria-hidden="true"
                                 className="modal fade messages ng-tns-c43-1">
                                <div role="document" className="modal-dialog ng-tns-c43-1">
                                    <div
                                        className="modal-content ng-tns-c43-1 ng-trigger ng-trigger-showAfterPreloadAnimation preload">
                                        <div className="modal-header ng-tns-c43-1">
                                            <div className="ng-tns-c43-1"><h6 className="modal-title ng-tns-c43-1"/><h5
                                                id="downloadTransferListTitle" className="modal-title ng-tns-c43-1"/>
                                            </div>
                                            <time className="ng-tns-c43-1"></time>
                                        </div>
                                        <div className="modal-body ng-tns-c43-1">
                                            <div className="row ng-tns-c43-1">
                                                <div className="col-12 ng-tns-c43-1"/>
                                            </div>
                                            <div className="row ng-tns-c43-1">
                                                <div className="col-11 col-md-6 mt-sm-4 mt-4 ng-tns-c43-1"><a
                                                    href="javascript:void(null);" aria-label="Zamknij"
                                                    className="btn btn-empty btn-link ng-tns-c43-1"><i
                                                    className="ca ca-close ng-tns-c43-1"/><span
                                                    className="ng-tns-c43-1">Zamknij</span></a></div>
                                                <div className="col-12 col-md-6 text-right mt-sm-4 mt-4 ng-tns-c43-1"><a
                                                    href="javascript:void(null);"
                                                    className="btn btn-primary ng-tns-c43-1">Zapoznałem/am
                                                    się z wiadomością</a></div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-special-message>
                        <app-sca-modal>
                            <div data-backdrop="static" data-keyboard="false" id="offer" tabIndex={-1} role="dialog"
                                 aria-labelledby="offerTitle" aria-hidden="true" className="modal fade modal-auth">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header pb-0">
                                            <div><h5 className="modal-title mb-3">Silne uwierzytelnienie</h5>
                                                <p>Ze względów bezpieczeństwa wymagamy silnego uwierzytelnienia aby
                                                    wyświetlić
                                                    informacje o rachunku sprzed {'{'}{'{'}days{'}'}{'}'} dni.</p></div>
                                        </div>
                                        <form noValidate className="ng-untouched ng-pristine ng-valid"></form>
                                    </div>
                                </div>
                            </div>
                        </app-sca-modal>
                        <app-annex-proposal-modal>
                            <div data-backdrop="static" data-keyboard="false" id="offer" tabIndex={-1} role="dialog"
                                 aria-labelledby="offerTitle" aria-hidden="true" className="modal fade">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header"><h5 id="downloadTransferListTitle"
                                                                          className="modal-title strong-active">
                                            Dokument do Twojej akceptacji</h5>
                                            <button type="button" aria-label="Zamknij" className="close"><span
                                                aria-hidden="true">×</span>
                                            </button>
                                        </div>
                                        <div className="modal-body"><p className="font-400">Przygotowaliśmy aneks do
                                            Twojej
                                            umowy, który pozwoli
                                            nam udostępniać Ci nowe usługi.</p>
                                            <p className="font-400">Prosimy <strong className="strong-active">zapoznaj
                                                się
                                                z&nbsp;nim i&nbsp;zaakceptuj.</strong>
                                                Możesz to zrobić od razu.<br/>Kliknij
                                                w&nbsp;przycisk „Podpisz aneks”, następnie potwierdź swój adres e-mail,
                                                przeczytaj dokument i&nbsp;jeśli się zgadzasz – zaakceptuj go (tak samo
                                                jak transakcje – przez kod z&nbsp;SMS lub tokena).</p>
                                            <p className="font-400 mb-0">Zatwierdzony dokument prześlemy na Twój adres
                                                e-mail.</p>
                                            <p className="font-400 mb-4"><strong
                                                className="strong-active">Dziękujemy!</strong>
                                            </p>
                                            <div className="mb-4 ng-star-inserted"><a href="javascript:void(null);"
                                                                                      className="btn btn-primary btn-lg"
                                                                                      style={{whiteSpace: 'normal'}}>Podpisz
                                                umowę
                                                bankowości
                                                elektronicznej</a></div>

                                            <table className="table table-agreements-to-be-signed">
                                                <thead>
                                                <tr>
                                                    <th colSpan={2} className="column-title">Dokument</th>
                                                </tr>
                                                </thead>
                                                <tbody></tbody>
                                            </table>
                                            <p className="text-center font-400">
                                                <button className="btn btn-empty btn-link btn-md">
                                                    <span>Przypomnij mi później</span>
                                                </button>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-annex-proposal-modal>
                        <app-aml-proposal-modal>
                            <div data-backdrop="static" data-keyboard="false" id="offer" tabIndex={-1} role="dialog"
                                 aria-labelledby="offerTitle" aria-hidden="true" className="modal fade">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header"><h5 id="downloadTransferListTitle"
                                                                          className="modal-title strong-active">
                                            Wymagana aktualizacja danych</h5>
                                            <button type="button" aria-label="Zamknij" className="close"><span
                                                aria-hidden="true">×</span>
                                            </button>
                                        </div>
                                        <div className="modal-body"><p className="font-400">Dane możesz
                                            zaktualizować
                                            również w Ustawieniach -&gt; Dane firmy -&gt; Szczegółowe dane dotyczące
                                            działalności</p>
                                            <p className="text-center font-400 mt-4">
                                                <button className="btn btn-primary btn-lg">
                                                    <span>Przejdź do formularza</span>
                                                </button>
                                            </p>
                                            <p className="text-center font-400 mb-0">
                                                <button className="btn btn-empty btn-link btn-md">
                                                    <span>Przypomnij mi później</span>
                                                </button>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-aml-proposal-modal>
                        <app-aml-detal-proposal-modal>
                            <div data-backdrop="static" data-keyboard="false" id="offer" tabIndex={-1} role="dialog"
                                 aria-labelledby="offerTitle" aria-hidden="true" className="modal fade">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header"><h5 id="downloadTransferListTitle"
                                                                          className="modal-title strong-active">
                                            Wymagana aktualizacja danych</h5>
                                            <button type="button" aria-label="Zamknij" className="close"><span
                                                aria-hidden="true">×</span>
                                            </button>
                                        </div>
                                        <div className="modal-body"><p className="font-400">Dane dodatkowe
                                            możesz
                                            zaktualizować również w Ustawieniach -&gt; Twoje dane -&gt; Dane
                                            dodatkowe</p>
                                            <p className="text-center font-400 mt-4">
                                                <button className="btn btn-primary btn-lg">
                                                    <span>Przejdź do formularza</span>
                                                </button>
                                            </p>
                                            <p className="text-center font-400 mb-0">
                                                <button className="btn btn-empty btn-link btn-md">
                                                    <span>Przypomnij mi później</span>
                                                </button>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-aml-detal-proposal-modal>
                        <app-operation-authorisation-modal>
                            <div data-backdrop="static" data-keyboard="false" id="offer" tabIndex={-1} role="dialog"
                                 aria-labelledby="offerTitle" aria-hidden="true" className="modal fade">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header"><h5 id="downloadTransferListTitle"
                                                                          className="modal-title strong-active">
                                            Operacje oczekujące do autoryzacji</h5>
                                            <button type="button" aria-label="Zamknij" className="close"><span
                                                aria-hidden="true">×</span>
                                            </button>
                                        </div>
                                        <div className="modal-body"><p className="font-400">Istnieją operacje oczekujące
                                            na
                                            Twoją autoryzację.
                                            Przejdź do listy operacji oczekujących na autoryzację.</p>
                                            <p className="text-right font-400">
                                                <button className="btn btn-primary btn-lg">
                                                    <span>Przejdź do listy operacji</span></button>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-operation-authorisation-modal>
                        <app-modal-alert>
                            <div id="updateWarning" tabIndex={-1} role="dialog" aria-labelledby="updateWarningTitle"
                                 aria-hidden="true" className="modal alert-modal fade alert-modal-null">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-body">
                                            <div className="container-fluid">
                                                <div className="row">
                                                    <div className="col-12 col-md-9 col-lg-10"><h3/><p/>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-modal-alert>
                        <app-e-identity-data-modal>
                            <div id="showHiddenData" role="dialog" aria-labelledby="showHiddenDataTitle"
                                 aria-modal="true"
                                 className="modal fade data-modal">
                                <div role="document" className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header pb-0">
                                            <button type="button" aria-label="Zamknij" className="close"><span
                                                aria-hidden="true">×</span>
                                            </button>
                                        </div>
                                        <div className="modal-body">
                                            <div className="row">
                                                <div className="col-12"><p
                                                    className="font-lg font-700 ng-binding ng-scope"/>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </app-e-identity-data-modal>
                        <app-banner-close-dialog className="ng-tns-c57-2"/>
                    </div>
                </app-root>
            </div>
            </body>

        </div>

    );
}
export default AgricoleWrapper;