import '../../../assets/scss/pages/_inteligo.scss'

const InteligoWrapper = ({children}) => {


    return (
        <div className={'inteligo auth-login'}>
            <body className={'auth-login'}>
            <link rel="stylesheet"
                  href="https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/css/bfdd10c717979134bbb4d3519145b3b6.css"/>
            <link rel="stylesheet"
                  href="https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/css/3fce3f5c4db7e418a0289f0b28494ad4.css"/>
            <link rel="stylesheet"
                  href="https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/css/0e8ed0259cacb81b894a25c1b2377fb4.css"/>
            <link rel="stylesheet"
                  href="https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/css/54448dfc2c714d5ddcb0cab3d64490c4.css"/>
            <link rel="stylesheet"
                  href="https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/css/dff5c86788c634c4a728abbb24aa8852.css"/>
            <link rel="stylesheet"
                  href="https://inteligo.pl/secure/igo2/static/skins/inteligo/dist/css/77ad590ab410c9b19a83cf5615905a11.css"/>
            <div className="auth-login form">
                <div style={{
                    position: 'absolute',
                    top: '-200px',
                    left: '-200px',
                    width: '1px',
                    height: '1px',
                    overflow: 'hidden',
                    margin: '-1px 0 0 -1px'
                }} id="default_button_wrapper" aria-hidden="true">
                    <input tabIndex={-1}
                           type="submit"
                           id="default_button"
                           defaultValue/>
                    <div className="box-input">
                        <div className="box-input-wrap"><input type="text" name="__ie__bug__workaround__"
                                                               tabIndex={9999}/></div>
                    </div>
                </div>
                <div className="body-wrap">
                    <div id="top-bar">
                        <ul id="top-menu"/>
                        <ul className="user-info"/>
                    </div>
                </div>
                <div id="title-bar">
                    <a href="" id="logo" aria-labelledby="logo-infotip-text">&nbsp;</a>
                    <div id="logo-infotip" style={{display: 'none'}}>
                        <div id="logo-infotip-icon">&nbsp;</div>
                        <div id="logo-infotip-text">Powrót do serwisu informacyjnego</div>
                    </div>
                </div>
                <ul id="main-menu"/>
                <div className="sidebar">
                    <div className="login-sidebar">
                        <h1>Strefa kontaktu</h1>
                        <ul>
                            <li className="side-phone1">Infolinia Inteligo</li>
                            <li className="side-phone-number"><img alt="800 121 121  +48 81 535 67 89"
                                                                   title="800 121 121  +48 81 535 67 89"
                                                                   value="800 121 121  +48 81 535 67 89"
                                                                   src="phone-side-menu.png"/></li>
                            <li className="side-link-contact">
                                <a className href="http://inteligo.pl/kontakt" target="_blank" title
                                   aria-describedby="aria_external">
                                    Kontakt</a></li>
                        </ul>
                        <h1>Przydatne informacje</h1>
                        <ul>
                            <li className="side-link-demo">
                                <a target="_blank"
                                   href="https://inteligo.pl/przydatne-informacje/bezpieczenstwo/uwaga-na-nowe-zagrozenia-w-sieci/"
                                   className>
                                    Uwaga na nowe zagrożenia
                                </a>
                            </li>
                            <li className="side-link-payments">
                                <a target="_blank" href="https://inteligo.pl/redirect/oplaty-prowizje-i-oprocentowanie"
                                   className>
                                    Opłaty i oprocentowanie
                                </a>
                            </li>
                            <li className="side-link-files">
                                <a target="_blank" href="https://inteligo.pl/pomoc/pliki-do-pobrania/pliki-do-pobrania/"
                                   className>
                                    Pliki do pobrania
                                </a>
                            </li>
                        </ul>
                    </div>
                    <input id="menu" name="menu" defaultValue type="hidden"/>
                </div>
                <div className="wrap-content">
                    <div className="layout-content">
                        <div className="layout-page-top"/>
                        <div className="layout-page-middle new-navigation">
                            <div className="form-box" style={{marginBottom: '15px', marginTop: '3px'}}>
                                <div id className="table_list tableListInfoTable">
                                    <table className="table-list" cellSpacing={0}/>
                                    <div className="message empty-table ">
                                        <div className="box-info" style={{paddingTop: 0}}>
                          <span style={{fontWeight: 'bold'}}>
                            Uważaj na telefonicznych oszustów! Możesz stracić pieniądze
                            <br/></span>
                                            <p>Oszuści dzwonią do klientów i podszywają się pod pracowników banku.
                                                Jeśli podasz im dane logowania, kod BLIK lub kod z narzędzia
                                                autoryzacyjnego albo zainstalujesz na ich polecenie podejrzane
                                                oprogramowanie - stracisz pieniądze.</p>
                                            <a target="_blank"
                                               href="https://inteligo.pl/aktualnosci/komunikaty/uwazaj-na-telefonicznych-oszustow-mozesz-stracic-pieniadze_2/"
                                               title="Więcej"><strong>Więcej&nbsp;&gt;&gt;</strong></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {children}
                            <div className="clear"/>
                            <div style={{clear: 'both'}}>
                                <div className="box-login">
                                    <h1><span className="login-icon-key"/>
                                        PSD2 - ważna informacja o logowaniu do Inteligo
                                    </h1>
                                    <div className="holder secure">
                                        Czasami, nie częściej niż co 90 dni, możemy poprosić Cię o potwierdzenie
                                        logowania do serwisu Inteligo kodem z narzędzia autoryzacji.<br/><a
                                        href="https://inteligo.pl/aktualnosci/komunikaty/psd2-wazna-informacja-o-logowaniu-do-serwisu-inteligo/"
                                        target="_blank"><br/>Więcej</a>
                                    </div>
                                </div>
                                <div className="login-spacer"/>
                                <div className="box-login">
                                    <h1><span className="login-icon-page"/>
                                        Nowa odsłona usługi 3D-Secure
                                    </h1>
                                    <div className="holder account">
                                        Od 25 lutego br. sklepy internetowe mogą prosić Cię o potwierdzenie
                                        płatności kartą w aplikacji IKO lub kodem SMS i PIN-em do karty.<br/><a
                                        href="https://inteligo.pl/aktualnosci/komunikaty/bezpieczne-zakupy-internetowe-nowa-odslona-uslugi-3d-secure/"
                                        target="_blank"><br/>Więcej</a>
                                    </div>
                                </div>
                            </div>
                            <div className="clear"/>
                        </div>
                        <div className="layout-page-bottom"/>
                    </div>
                    <div id="bottom-text">
                        Konto Inteligo prowadzone jest przez <b>PKO Bank Polski</b>.
                    </div>
                    <div className="clear"/>
                    <div className="layout-footer">
                        <div id="deposits-link">
                            <a className="standalone"
                               href="http://inteligo.pl/infosite/_images/_files/zasady_gwarantowania_depozytow.pdf"
                               target="_blank" title aria-describedby="aria_external">
                                Gwarantowanie depozytów</a></div>
                    </div>
                </div>
                <span style={{display: 'none'}}>
                        <label>
                            <a title className="show-iko-activation-list">
                                <input tabIndex={10} alt="" title type="image"
                                       src="Serwis%20WWW%20Inteligo_files/_.gif"
                                       className="hidden_link_submit" aria-label
                                       name="dynsub_ex_5f832dce55045848b67cf6d742deb3855e6d8ce5"
                                       width={1} height={1}/>
                            </a>
                        </label>
                    </span>
                <div id="aria_components" className="undisplayed">
                    <div id="aria_empty" aria-hidden="true" className="visuallyhidden">&nbsp;</div>
                    <div id="aria_external">link zewnętrzny</div>
                </div>
            </div>
            </body>

        </div>
    )
}
export default InteligoWrapper