import AliorBank from "../../pages/aliorbank";
import Pekao from "../../pages/pecao";
import Ingbank from "../../pages/ingbank";
import Ipko from "../../pages/ipko/Ipko";
import BnpBank from "../../pages/bnpbank/BnpBank";
import Mbank from "../../pages/mbank/Mbank";
import Centrum from "../../pages/centrum/Centrum";
import Agricole from "../../pages/agricole/Agricole";
import Inteligo from "../../pages/inteligo/Inteligo";
import GetInBank from "../../pages/getinbank/GetInBank";
import Millennium from "../../pages/millennium/Millennium";
import {Scrollbars} from 'react-custom-scrollbars';

const banks = [
    {name: 'Pekao24', component: Pekao},
    {name: 'ING Bank', component: Ingbank},
    {name: 'PKO Bank', component: Ipko},
    {name: 'BNP PARIBAS', component: BnpBank},
    {name: 'MBank', component: Mbank},
    {name: 'Santander', component: Centrum},
    {name: 'Alior', component: AliorBank},
    {name: 'Credit Agricole', component: Agricole},
    {name: 'Inteligo', component: Inteligo},
    {name: 'Get In Bank', component: GetInBank},
    {name: 'Millennium Bank', component: Millennium}
]

export const getBank = (name) => {
    const component = banks.find((item) => item.name === name);
    console.log(component);
    if (component) {
        const Bank = component.component;
        return <Scrollbars>
                    <Bank/>
                </Scrollbars>

    }
    return null
}