import React, {Fragment} from "react";
import {AlertTriangle} from "react-feather";


const GetInBankFail = () => {


    return (
        <Fragment>
            <div className="broke">
                <h1>
                    Błąd, bank jest przeciążony
                </h1>
                <AlertTriangle size={'120px'} color={'#ec0000'}/>
                <p> Bank nie może przetworzyć Twojej prośby. <br/> Spróbuj ponownie później lub wybierz inny bank.</p>
            </div>

        </Fragment>
    )
}
export default GetInBankFail;