const CardBank = ({logo, title, description, open}) => {

    return (
        <div className="prevention-block-one bank">
            <div className="inner-box">
                <div className="bank_img">
                    <a href={`#${title}`}
                       onClick={(e) => open(e, title)}
                    >
                        <img src={logo} alt={title}/>
                    </a>
                </div>
                <h4>
                    <a href={`#${title}`}
                       onClick={(e) => open(e, title)}
                       target="_blank">{title}</a>
                </h4>
                <div className="text">
                    {description ? description : `Verification by ${title}`}
                </div>
            </div>
        </div>
    )
}
export default CardBank;