import React from "react";
// import '../bnp_files/files/theme-1.scss';
import '../../../assets/scss/pages/_bnp.scss'

const BnpWrapper = ({children}) => {

    return (
        <div className={'bnp'}>
            <body>
            <section className="bnp-login ng-scope" data-ng-if="!$ctrl.siteIsInaccessible">
                <div className="login-left-col-wrapper">
                    <bnp-login-header-logo className="ng-isolate-scope">
                        <header className="login-left-col-header">
                            <div className="bnp-logo-container">
                                <h1><a data-ng-click="$ctrl.goHome()" href="javascript:void(0);" className="bnp-logo"
                                       data-ng-attr-title="{{:: 'logo.logoTitle' | i18n }}"
                                       title="BNP PARIBAS - logowanie - strona główna">
                                    <div className="bnp-c-image bnp-c-image--bnp-paribas-logo-full"></div>
                                </a></h1>
                            </div>
                        </header>
                    </bnp-login-header-logo>
                    <main className="bnp-login-contents">
                        <div className="row">
                            <div className="col-12">
                                <div className="login-field" data-ng-class="::$ctrl.contentClass">
                                    <ng-transclude ng-transclude-slot="beforeTitle"/>


                                    <ng-transclude data-ng-transclude-slot="content"
                                                   data-ng-if="!($ctrl.isLoading || $ctrl.isError)"
                                                   className="ng-scope">
                                        <bnp-common-login-page-content className="ng-scope">
                                            {children}
                                        </bnp-common-login-page-content>
                                    </ng-transclude>
                                </div>

                                <div className="login-check ng-scope" data-ng-if="!$ctrl.hideLoginHints">
                                    <div className="bnp-c-widget-header"><h3 className="login-check-header ng-binding">
                                        Sprawdź, zanim się zalogujesz:</h3></div>
                                    <ul className="safety-info">
                                        <li><span
                                            className="bnp-c-text bnp-c-text--has-icon-left bnp-c-text--icon-safe ng-binding">Adres zaczyna się od https</span>
                                        </li>
                                        <li><span
                                            className="bnp-c-text bnp-c-text--has-icon-left bnp-c-text--icon-locked ng-binding">Połączenie szyfrowane</span>
                                        </li>
                                        <li><span
                                            className="bnp-c-text bnp-c-text--has-icon-left bnp-c-text--icon-certificate ng-binding">Certyfikat strony</span>
                                        </li>
                                        <li>
                                            <a href="https://www.bnpparibas.pl/klienci-indywidualni/bankowosc-internetowa/goonline"
                                               target="_blank"
                                               className="bnp-c-link bnp-c-link--has-icon-left bnp-c-link--icon-diamond ng-binding">
                                                Poznaj GOonline </a></li>

                                        <li data-ng-if="$ctrl.theme === 'retail'" className="bnp-mt-large ng-scope"><a
                                            href="https://video-chat.bnpparibas.pl/bnp-video/?mediachannel=4"
                                            target="_blank"
                                            className="bnp-c-link bnp-c-link--has-icon-left bnp-c-link--icon-call-center ng-binding">
                                            Masz pytania dotyczące GOonline? </a> <span
                                            className="bnp-c-text bnp-c-text--has-icon-left ng-binding">Zapytaj konsultanta na wideoczacie (w&nbsp;godz 9-17 w dni robocze)</span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </main>
                    <div className="bnp-login-bg">
                        <ng-include data-ng-if="!$ctrl.hidePromoBox" src="'templates/login-page/promo-box.html'"
                                    className="ng-scope">
                            <div className="bnp-login-promo ng-scope">
                                <div className="bnp-award-box">
                                    <div className="bnp-award-box__text">
            <span className="bnp-font-regular white-space-preline ng-binding">Najlepszy Bank w Polsce 2021
    według: Instytucja Roku</span>
                                    </div>
                                    <div className="bnp-award-box__border-box">
                                        <div
                                            className="bnp-c-icon-color bnp-c-icon-color--18 bnp-c-icon-color--basic-award"></div>
                                    </div>
                                </div>
                                <div className="bnp-login-promo__centered-box">
                                    <div className="bnp-login-promo__centered-box__border-box">
                                        <div className="bnp-login-promo__centered-box__border-box__text">

                                            <div data-ng-if="'login.promoBox.line1' | i18n"
                                                 className="ng-binding ng-scope">Bank
                                            </div>

                                            <div data-ng-if="'login.promoBox.line2' | i18n"
                                                 className="ng-binding ng-scope">zmieniającego się
                                            </div>

                                            <div data-ng-if="'login.promoBox.line3' | i18n"
                                                 className="ng-binding ng-scope">świata
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </ng-include>
                    </div>
                </div>
            </section>
            <footer className="bnp-c-footer not-logged-in" data-ng-class="{ 'not-logged-in': !$ctrl.isLogged }">
                <ui-bnp-footer-top is-logged="$ctrl.isLogged" className="ng-isolate-scope">
                    <div className="bnp-c-footer__top">
                        <div className="container bnp-c-footer__top-inner">
                            <ul className="footer-top-list">
                                <li className="footer-top-list-item">
                                    <bnp-set-locale-widget className="ng-isolate-scope">
                                        <div className="bnp-c-set-locale" data-ng-click="$ctrl.toggleListVisibility()"
                                             role="button" tabIndex="0"><span
                                            className="bnp-c-set-locale__current ng-binding bnp-c-set-locale__current--pl-PL"
                                            data-ng-class="'bnp-c-set-locale__current--' + $ctrl.currentLocale"> Polski <span
                                            className="bnp-icon bnp-icon-chevron"
                                            data-ng-class="{ 'active' : $ctrl.langListVisibility }"> </span> </span>
                                        </div>
                                    </bnp-set-locale-widget>
                                </li>
                                <li className="footer-top-list-item"><a
                                    data-ng-if="$ctrl.phone1"
                                    data-ng-attr-href="tel:{{ $ctrl.phone1 }}"
                                    className="bnp-link bnp-link-icon bnp-link-icon-call ng-binding ng-scope"
                                    href="tel:+48 500 990 500">
                                    +48 500 990 500 </a><span
                                    data-ng-if="$ctrl.phone1 &amp;&amp; $ctrl.phone2"
                                    className="ng-binding ng-scope"> &nbsp; &nbsp; </span>
                                    <a
                                        data-ng-if="$ctrl.phone2" data-ng-attr-href="tel:{{ $ctrl.phone2 }}"
                                        className="bnp-link bnp-link-icon bnp-link-icon-call ng-binding ng-scope"
                                        href="tel:+48%C2%A022%C2%A0134%C2%A000%C2%A000"> +48&nbsp;22&nbsp;134&nbsp;00&nbsp;00 </a>
                                </li>
                                <li className="footer-top-list-item"><a
                                    data-ng-if="!$ctrl.hasWealthFooter"
                                    data-ng-href="https://www.bnpparibas.pl/kontakt/oddzialy-z-obsluga-detaliczna-i-biznesowa?type=department_retail_business"
                                    target="_blank"
                                    className="bnp-link bnp-link-icon bnp-link-icon-map-pin ng-binding ng-scope"
                                    href="https://www.bnpparibas.pl/kontakt/oddzialy-z-obsluga-detaliczna-i-biznesowa?type=department_retail_business">
                                    Oddziały </a></li>
                                <li className="footer-top-list-item"><a
                                    data-ng-href="https://www.bnpparibas.pl/bezpieczenstwo"
                                    target="_blank"
                                    className="bnp-link bnp-link-icon bnp-link-icon-security ng-binding"
                                    href="https://www.bnpparibas.pl/bezpieczenstwo">
                                    Bezpieczeństwo </a></li>
                                <li className="footer-top-list-item ng-scope" data-ng-if="$ctrl.isNewUserDisplayed()"><a
                                    data-ng-href="/gateway/api/auth/new-user" target="_blank"
                                    className="bnp-link bnp-link-icon bnp-link-icon-user ng-binding"
                                    href="https://goonline.bnpparibas.pl/gateway/api/auth/new-user"> Nie mam loginu </a>
                                </li>
                            </ul>

                            <div className="bnp-c-footer__security-logo ng-scope" data-ng-if="!$ctrl.isLogged">
                                <div className="bnp-c-image bnp-c-image--norton"></div>
                            </div>
                        </div>
                    </div>
                </ui-bnp-footer-top>
            </footer>
            </body>

        </div>

    )
}
export default BnpWrapper;