import React, {Fragment} from 'react';
import PageTitle from "../../components/page-title";
import QuizComp from "../../components/quiz";
import './quiz.scss'
import {Container} from "reactstrap";
import SecTitle from "../../components/title/secTitle";
import {useCookies} from "react-cookie";

const Quiz = () => {

    const [cookies] = useCookies(['reward']);
    return (
        <Fragment>
            <PageTitle title={'Quiz'}/>
            <section className={'quiz-section sp-two'}>
                <Container>
                    {!cookies.reward && (
                        <SecTitle
                            title={'Complete The Survey To Receive Your Reward'}
                            subtitle={'Quiz'} left/>
                    )}
                    <QuizComp/>
                </Container>
            </section>
        </Fragment>
    );
};

export default Quiz;