import React, {Fragment, useEffect, useState} from 'react';
import bankIdImg from '../../assets/img/bankid-vector-logo.svg'
import {getBank} from "../../utils/getBank/getBank";
import {CSSTransition} from "react-transition-group";

const ModalBankContent = ({bank}) => {

    const [loading, setLoading] = useState(true);

    console.log(loading)
    useEffect(() => {
        setLoading(true)
        setTimeout(() => {
            setLoading(false);
        }, 3000)
    }, [])

    return (
        <Fragment>
            <CSSTransition classNames={'load_bank'} in={loading} timeout={300}>
                <div className={'load_bank'}>
                    <img src={bankIdImg} alt=""/>
                </div>
            </CSSTransition>
            {getBank(bank)}
        </Fragment>
    )

};

export default ModalBankContent;